import React, { useState, useEffect } from "react";
import { createMuiTheme, ThemeProvider, makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import Hidden from '@material-ui/core/Hidden';
import Drawer from '@material-ui/core/Drawer';
import Box from '@material-ui/core/Box';
import Switch from '@material-ui/core/Switch';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import * as Services from "../../services/services";
import TelegramTokenItem from "./telegramtokenitem";
import ProxyItem from "./proxyitem";
import MailItem from "./mailitem";
import UriMoreConnectionsItem from "./urimoreconnectionsitem";
import UriMoreVisitedItem from "./urimorevisiteditem";
import ConnectionsWithoutUserItem from "./connectionswithoutuseritem"
import UsersMultipleIp from "./usersmultipleipitem"
import UserNotifyConnection from "./usernotifyconnection"

const drawerWidth = 230;

const useStyles = makeStyles((theme) => ({
  appBar: {
    [theme.breakpoints.up('lg')]: {
      width: `100%`,//calc(100% - ${drawerWidth}px)
      marginLeft: 230
    },
    zIndex: theme.zIndex.drawer + 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
    [theme.breakpoints.up('lg')]: {
      display: 'none',
    }
  },
  drawer: {
    [theme.breakpoints.up('lg')]: {
      width: drawerWidth,
      flexShrink: 0,
    },
  },
  toolbar: theme.mixins.toolbar,
  drawerPaper: {
    width: drawerWidth,
    borderRight: 'none',
    overflowX: 'hidden'
  },
  rounded: props => ({
    color: props.prefersDarkMode ? theme.palette.text.secondary : theme.palette.secondary,
    backgroundColor: props.prefersDarkMode ? '#343A40' : theme.palette.grey['400']
  }),
  label: props => ({
    color: props.prefersDarkMode ? theme.palette.text.secondary : theme.palette.secondary
  }),
  /*{
   color: theme.palette.text.secondary,
   backgroundColor: props.prefersDarkMode ? '#212529' : '#fafafa'///'#343A40',
 },*/
  marginLeft: {
    marginLeft: '10px'
  },
  listText: props => ({
    color: props.prefersDarkMode ? theme.palette.text.secondary : theme.palette.secondary
  })
}))

export default function Sidebar(props) {
  const classes = useStyles(props);
  const [response, setResponse] = useState(null);

  const theme = React.useMemo(
    () =>
      createMuiTheme({
        palette: {
          type: props.prefersDarkMode ? 'dark' : 'light',
          primary: {
            light: '#757ce8',
            main: '#dc3545', //'#3f50b5',
            dark: '#002884',
            contrastText: '#fff',
          },
          secondary: {
            light: '#ff7961', //focus
            main: '#dc3545', //background
            dark: '#ba000d', //hover
            contrastText: '#fff', // color
          },
          text: {
            secondary: '#9FA2A5'
          },
          background: {
            paper: props.prefersDarkMode ? '#212529' : '#e9ecef',
            default: props.prefersDarkMode ? '#212529' : '#fafafa'
          }
        },
      }),
    [props.prefersDarkMode],
  );

  const handleChangeConfig = async (e, config) => {
    e.preventDefault()

    await Services.updateConfig(config).then((response) => {
      loadConfig()
    }).catch((error) => {
      props.showMessage('Lo sentimos ha ocurrido un error inesperado.', 'error')
    })
  }

  const loadConfig = async () => {
    await Services.loadconfig().then((response) => {
      console.log(response.data.config);
      setResponse(response.data)
    }).catch((error) => {
      return
    })
  }

  useEffect(() => {
    loadConfig()
  }, []);

  const drawer = (
    <React.Fragment>
      <div className={classes.toolbar} style={{ marginTop: '1.6rem' }}/>
      <label className={classes.marginLeft}>REPORTES</label>
      <List>

        <UsersMultipleIp connections={props.connections} prefersDarkMode={props.prefersDarkMode} />

        <ConnectionsWithoutUserItem connections={props.connections} prefersDarkMode={props.prefersDarkMode} />

        <UriMoreConnectionsItem connections={props.connections} prefersDarkMode={props.prefersDarkMode} />

        <UriMoreVisitedItem connections={props.connections} prefersDarkMode={props.prefersDarkMode} />

      </List>
      <label className={classes.marginLeft}>CONFIGURAR</label>
      <List>

        <MailItem showMessage={props.showMessage} loadConfig={loadConfig} mail={response !==  null && response.mail !==  null && response.mail !==  undefined ? response.mail : null} prefersDarkMode={props.prefersDarkMode} />

        <ProxyItem showMessage={props.showMessage} loadConfig={loadConfig} proxy={response !==  null && response.proxy !==  null && response.proxy !==  undefined ? response.proxy : null} prefersDarkMode={props.prefersDarkMode} />

        <TelegramTokenItem showMessage={props.showMessage} loadConfig={loadConfig} bot={response !==  null && response.bot !==  null && response.bot !==  undefined ? response.bot : null} prefersDarkMode={props.prefersDarkMode} handleRefreshChats={props.handleRefreshChats} />

      </List>
      <label className={classes.marginLeft} >NOTIFICACIONES</label>
      <List>

        <UserNotifyConnection showMessage={props.showMessage} prefersDarkMode={props.prefersDarkMode} server={props.server}/>
 
      </List>
      <Box display="flex" flexDirection="column" style={{ width: '100%', marginLeft: '1rem' }}>
        <Box>
          <FormControlLabel
            classes={{
              label: classes.label,
            }}
            control={<Switch
              id={"emailnotification"}
              checked={(response !==  null && response.config !==  null && response.config !==  undefined && response.config.mailnotify)}
              onChange={(e) => { handleChangeConfig(e, { mailnotify: (response !==  null && response.config !==  null && response.config !==  undefined) ? !response.config.mailnotify : true }) }}
              name={"emailnotification"}
              inputProps={{ 'aria-label': 'secondary checkbox' }}
            />}
            label="Por correo"
          />
        </Box>
        <Box>
          <FormControlLabel
            classes={{
              label: classes.label,
            }}
            control={<Switch
              id={"emailnotification"}
              checked={(response !==  null && response.config !==  null && response.config !==  undefined && response.config.botnotify)}
              onChange={(e) => { handleChangeConfig(e, { botnotify: (response !==  null && response.config !==  null && response.config !==  undefined) ? !response.config.botnotify : true }) }}
              name={"emailnotification"}
              inputProps={{ 'aria-label': 'secondary checkbox' }}
            />}
            label="Por SQD-bot"
          />
        </Box>
      </Box>
    </React.Fragment>
  );

  return (
    <ThemeProvider theme={theme}>
      <nav className={classes.drawer} aria-label="mailbox folders">
        <Hidden smUp implementation="css">
          <Drawer
            container={props.container}
            variant="temporary"
            anchor={theme.direction === 'rtl' ? 'right' : 'left'}
            open={props.mobileOpen}
            onClose={props.handleDrawerToggle}
            classes={{
              paper: classes.drawerPaper,
            }}
            ModalProps={{
              keepMounted: true,
            }}
          >
            {drawer}
          </Drawer>
        </Hidden>
        <Hidden mdDown implementation="css">
          <Drawer
            classes={{
              paper: classes.drawerPaper,
            }}
            variant="permanent"
            open
          >
            {drawer}
          </Drawer>
        </Hidden>
      </nav>
    </ThemeProvider>
  )
}