import React, { useState } from "react";
import { createMuiTheme, ThemeProvider, makeStyles } from '@material-ui/core/styles';
import Avatar from '@material-ui/core/Avatar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import ConfirmDialog from "../dialog/confirm";
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ChatOutlinedIcon from '@material-ui/icons/ChatOutlined';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import * as Services from "../../services/services"
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Alert from '@material-ui/lab/Alert';
import CircularProgress from '@material-ui/core/CircularProgress';
import CloseIcon from '@material-ui/icons/Close';
import ThumbUpAltIcon from '@material-ui/icons/ThumbUpAlt';
import TextField from '@material-ui/core/TextField';
import { green } from '@material-ui/core/colors';

const useStyles = makeStyles((theme) => ({
    button: {
        margin: '0',
        minWidth: '0'
    },
    typography: {
        marginBottom: '0',
        marginLeft: '10px'
    },
    rounded: props => ({
        color: props.prefersDarkMode ? theme.palette.text.secondary : theme.palette.secondary,
        backgroundColor: props.prefersDarkMode ? '#343A40' : theme.palette.grey['400']
    }),
    listText: props => ({
        color: props.prefersDarkMode ? theme.palette.text.secondary : theme.palette.secondary
    }),
    wrapper: {
        margin: theme.spacing(1),
        position: 'relative',
    },
    buttonProgress: {
        color: green[500],
        position: 'absolute',
        top: '50%',
        left: '50%',
        marginTop: -12,
        marginLeft: -12,
    }
}));

var validator = require('validator');

export default function TelegramTokenItem(props) {
    const theme = React.useMemo(
        () =>
            createMuiTheme({
                palette: {
                    type: props.prefersDarkMode ? 'dark' : 'light',
                    primary: {
                        light: '#757ce8',
                        main: '#dc3545', //'#3f50b5',
                        dark: '#002884',
                        contrastText: '#fff',
                    },
                    secondary: {
                        light: '#ff7961', //focus
                        main: '#dc3545', //background
                        dark: '#ba000d', //hover
                        contrastText: '#fff', // color
                    },
                    text: {
                        secondary: '#9FA2A5'
                    },
                    background: {
                        paper: props.prefersDarkMode ? '#343A40' : '#fff',
                        default: props.prefersDarkMode ? '#212529' : '#fafafa',
                    }
                },
            }),
        [props.prefersDarkMode],
    );

    const classes = useStyles(props);

    const [loading, setLoading] = useState(false);
    const [open, setOpen] = useState(false);

    const [newtoken, setNewToken] = useState('')

    const [error, setError] = useState({
        show: false,
        type: 'error',
        msg: ''
    });

    const handleClose = () => {
        setOpen(false);

        setLoading(false);

        setNewToken('')

        setError({
            show: false,
            type: 'error',
            msg: ''
        })
    };

    const handleChange = (e) => {
        e.preventDefault()
        setNewToken(e.target.value)
    }

    const handleSubmit = async (e) => {
        e.preventDefault()
        setError({
            show: false,
            type: 'error',
            msg: ''
        })

        if (validator.isEmpty(newtoken, { ignore_whitespace: true })) {
            setError(error => ({ ...error, show: true, msg: 'El token no es vlido' }))
        } else {
            if (!loading) {
                setLoading(true);
            }

            await Services.registerBot(newtoken).then((response) => {
                setLoading(false);
                if (response.data.errors) {
                    console.log('error', response.data.errors)
                    setError(error => ({ ...error, show: true, msg: response.data.errors[0].msg }))
                }
                else {
                    props.showMessage('Bot registrado correctamente', 'success')
                    handleClose()
                    props.loadConfig();
                    props.handleRefreshChats()

                }
            }).catch((error) => {
                setLoading(false);
                setError(error => ({ ...error, show: true, msg: 'Lo sentimos ha ocurrido un error inesperado.' }))
            });
        }
    }

    const handleRemoveClick = async () => {
        if (!loading) {
            setLoading(true);
        }
        await Services.deleteBot(props.bot._id).then((response) => {
            setLoading(false);
            if (response.data.errors)
                setError(error => ({ ...error, show: true, msg: response.data.errors[0].msg }))
            else {
                setLoading(false);
                setNewToken('')
                setError({
                    show: false,
                    type: 'error',
                    msg: ''
                })
                props.showMessage('Bot eliminado correctamente', 'success')
                props.loadConfig();
                props.handleRefreshChats()
            }
        }).catch((error) => {
            setLoading(false);
            setError(error => ({ ...error, show: true, msg: 'Lo sentimos ha ocurrido un error inesperado.' }))
        })
    }

    return (<React.Fragment>
        <ListItem onClick={(e) => { e.preventDefault(e); setOpen(true) }} button>
            <ListItemAvatar>
                <Avatar variant="rounded" className={classes.rounded}>
                    <ChatOutlinedIcon />
                </Avatar>
            </ListItemAvatar>
            <ListItemText primary="Token SQD-bot" className={classes.listText} />
        </ListItem>
        <ThemeProvider theme={theme}>
            {
                props.bot === null ?
                    <Dialog fullWidth={true} maxWidth="sm" open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
                        <form onSubmit={handleSubmit}>
                            <DialogTitle id="form-dialog-title">Token para SQD-bot</DialogTitle>
                            <DialogContent>
                                {
                                    error.show && <Alert variant="outlined" severity={error.type} style={{ marginBottom: '1rem' }}>{error.msg}</Alert>
                                }
                                <Box style={{ width: '100%' }}>
                                    <TextField style={{ width: '100%' }}
                                        onChange={handleChange}
                                        id="telegramitem-token"
                                        name="token"
                                        label="Token"
                                        variant="outlined"
                                        size="small"
                                        value={newtoken}
                                    />
                                </Box>
                            </DialogContent>
                            <DialogActions style={{ justifyContent: 'center', marginBottom: '0.5rem' }}>
                                <div className={classes.wrapper}>
                                    <Button
                                        type="submit"
                                        variant="contained"
                                        color="secondary"
                                        size="small"
                                        className={classes.button}
                                        startIcon={<ThumbUpAltIcon />}
                                        disabled={loading}
                                    >
                                        Aceptar
                                    </Button>
                                    {loading && <CircularProgress size={24} className={classes.buttonProgress} />}
                                </div>
                                <Button
                                    onClick={handleClose}
                                    variant="contained"
                                    color="default"
                                    size="small"
                                    className={classes.button}
                                    startIcon={<CloseIcon />}
                                >
                                    Cancelar
                                </Button>
                            </DialogActions>
                        </form>
                    </Dialog>
                    : <ConfirmDialog open={open} loading={loading} handleClose={handleClose} handleConfirmClick={handleRemoveClick} >
                        <Typography variant="h6" gutterBottom> Ya existe un token registrado en el sistema. Si desea aadir uno nuevo por favor confirme para eliminar el token actual:</Typography>
                        <Box display="flex" alignItems="center" flexGrow={1} >
                            <Typography gutterBottom className={classes.typography}>
                                <b> {props.bot.token} </b>
                            </Typography>
                        </Box>
                    </ConfirmDialog>
            }
        </ThemeProvider>
    </React.Fragment>);
}
