import React from 'react';
import * as Services from '../../services/services'
import { LineChart, Line, XAxis, YAxis, Tooltip, ResponsiveContainer } from 'recharts';

function Graphic(props) {
 
    const parseData = () => {
        let data = []
        props.data.map((value) => {
            let item = { label: value, value: value }
            if (props.type === 'size')
                item = { label: Services.getSize(value), value: value }
            else if (props.type === 'speed')
                item = { label: Services.getSize(value)+'/s', value: value }
            data.push(item)
            return value
        })
        return data
    }

    const CustomTooltip = (options) => {
        if (options.active && options.payload && options.payload.length) {
            return (
                <div style={{ backgroundColor: 'rgba(255,255,255,.15)', borderRadius: '0.5rem' }}>
                    <p className="label fs-6" style={{ padding: '5px' }}>{`${options.label}`}</p>
                </div>
            );
        }
        return null;
    };

    return (
        <div className="chart mt-2" style={{ height: props.graphicheight + 'px' }}>
            <ResponsiveContainer width="100%" height="100%">
                <LineChart width={300} height={100} data={parseData()}>
                    <Tooltip content={<CustomTooltip />} />
                    <YAxis hide={true} />
                    <XAxis dataKey="label" hide={true} />
                    <Line type="monotone" dataKey="value" stroke="#dc3545" strokeWidth={2} />
                </LineChart>
            </ResponsiveContainer>
        </div>
    );
}

export default Graphic;