import React, { useState, useEffect } from "react";
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Box from '@material-ui/core/Box';
import IconButton from '@material-ui/core/IconButton';
import ControlPointIcon from '@material-ui/icons/ControlPoint';
import ReplayIcon from '@material-ui/icons/Replay';
import CloseIcon from '@material-ui/icons/Close';
import ThumbUpAltIcon from '@material-ui/icons/ThumbUpAlt';
import TextField from '@material-ui/core/TextField';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Alert from '@material-ui/lab/Alert';
import CircularProgress from '@material-ui/core/CircularProgress';
import { green } from '@material-ui/core/colors';
import ServerItem from './serveritem'
import * as Services from "../../services/services"

var validator = require('validator');

const useStyles = makeStyles((theme) => ({
    paper: {
        padding: theme.spacing(2),
        color: theme.palette.text.secondary,
        borderRadius: '1rem'
    },
    button: {
        margin: '0',
        minWidth: '0'
    },
    typography: {
        marginBottom: '0',
        marginLeft: '10px'
    },
    wrapper: {
        margin: theme.spacing(1),
        position: 'relative',
    },
    buttonProgress: {
        color: green[500],
        position: 'absolute',
        top: '50%',
        left: '50%',
        marginTop: -12,
        marginLeft: -12,
    }
}));

export default function ServerItems(props) {
    const classes = useStyles();

    const [servers, setServers] = useState([])

    const [loading, setLoading] = useState(false);

    const [newserver, setNewServer] = useState({
        host: '',
        port: '3128',
        password: ''
    })

    const [error, setError] = useState({
        show: false,
        type: 'error',
        msg: ''
    });

    const [open, setOpen] = useState(false);

    const [serversHidden, setServersHidden] = useState(true)

    const handleClose = () => {
        setOpen(false);

        setNewServer({
            host: '',
            port: 3128,
            password: ''
        })

        setError({
            show: false,
            type: 'error',
            msg: ''
        })
    };

    const handleChange = (e) => {
        e.preventDefault()
        setNewServer(newserver => ({ ...newserver, [e.target.name]: e.target.value }));
    }

    const handleSubmit = async (e) => {
        e.preventDefault()
        setError({
            show: false,
            type: 'error',
            msg: ''
        })
        if (!validator.isIP(newserver.host)) {
            setError(error => ({ ...error, show: true, msg: 'La direccin ip del servidor no es vlida' }))
        } else
            if (!validator.isPort(newserver.port.toString())) {
                setError(error => ({ ...error, show: true, msg: 'El puerto no es vlido' }))
            } else
                if (validator.isEmpty(newserver.password, { ignore_whitespace: true })) {
                    setError(error => ({ ...error, show: true, msg: 'La contrasea no es vlida' }))
                } else {
                    if (!loading) {
                        setLoading(true);
                    }
                    await Services.addServer(newserver.host, newserver.port, newserver.password)
                        .then(async (response) => {
                            setLoading(false);
                            if (response.data.errors) {
                                setError(error => ({ ...error, show: true, msg: response.data.errors[0].msg }))
                            }
                            else {
                                props.showMessage('Servidor registrado correctamente!', 'success')
                                handleClose()
                                loadServers()
                                await Services.getConnectionsData()
                            }
                        }).catch((error) => {
                            setLoading(false);
                            setError(error => ({ ...error, show: true, msg: 'Lo sentimos ha ocurrido un error inesperado.' }))
                        });
                }
    }

    const handleDeleteServerClick = async (host, id) => {
        await Services.deleteServer(id).then(async (response) => {
            if (response.data.errors)
                props.showMessage(response.data.errors[0].msg, 'error')
            else {
                props.showMessage('Servidor ' + response.data.host + ' eliminado correctamente', 'success')
                loadServers();
                await Services.getConnectionsData()
            }
        }).catch((error) => {
            props.showMessage('Lo sentimos ha ocurrido un error inesperado.', 'error')
        })
    }

    const handleActivateServerClick = async (id) => {
        await Services.activateServer(id).then(async (response) => {
            if (response.data.errors)
                props.showMessage(response.data.errors[0].msg, 'error')
            else {
                props.showMessage('Servidor ' + response.data.host + ' activado correctamente', 'success')
                loadServers();
                await Services.getConnectionsData()
            }
        }).catch((error) => {
            props.showMessage('Lo sentimos ha ocurrido un error inesperado.', 'error')
        })
    }

    const loadServers = async (showMsg = false) => {
        await Services.getServers().then((response) => {
            if (response.data.length === 0) {
                setOpen(true);
            }
            setServers(response.data)
            if (response.data.errors && showMsg)
                props.showMessage(response.data.errors[0].msg, 'error')
            else
                if (showMsg)
                    props.showMessage('Lista de servidores refrescada!', 'success')
        }).catch((error) => {
            if (showMsg)
                props.showMessage('Lo sentimos ha ocurrido un error inesperado.', 'error')
        })
    }

    const handleRefreshClick = (e) => {
        e.preventDefault()
        loadServers(true)
    }

    useEffect(() => {
        async function getServers() {
            await Services.getServers().then((response) => {
                if (response.data.length === 0) {
                    setOpen(true);
                }
                setServers(response.data)
            }).catch((error) => {
                return
            })
        }
        getServers();
    }, [props.server.host]);

    return (
        <Grid item xs={12} sm={12} >
            <Paper className={classes.paper}>
                <Box display="flex" alignItems="center" flexGrow={1}>
                    <Box flexGrow={1}>
                        <Typography color="textPrimary" variant="h6">Servidores</Typography>
                    </Box>
                    <IconButton onClick={(e) => { e.preventDefault(); setOpen(true); }} edge="end" aria-label="add">
                        <ControlPointIcon />
                    </IconButton>
                    <IconButton onClick={(e) => {
                        handleRefreshClick(e)
                    }} edge="end" aria-label="delete">
                        <ReplayIcon />
                    </IconButton>
                </Box>
                <div style={{ width: '100%' }}>
                    {
                        servers.length > 0 &&
                        servers.slice(0, 3).map((server, index) => {
                            return <ServerItem loadServers={loadServers} showMessage={props.showMessage} key={'server' + index} index={index} server={server} handleActivateServerClick={handleActivateServerClick} handleDeleteServerClick={handleDeleteServerClick} />
                        })
                    }
                </div>
                {
                    servers.length > 3 &&
                    <React.Fragment>
                        <div style={{ width: '100%', display: serversHidden ? 'none' : 'inherit' }}>
                            {
                                servers.slice(3, servers.length).map((server, index) => {
                                    return <ServerItem loadServers={loadServers} showMessage={props.showMessage} key={'server' + index} index={index} server={server} handleActivateServerClick={handleActivateServerClick} handleDeleteServerClick={handleDeleteServerClick} />
                                })
                            }
                        </div>
                        <Box display="flex" alignItems="center" justifyContent="center">
                            <Button onClick={() => { setServersHidden(!serversHidden) }}>{serversHidden ? 'Ver todos' : 'Volver a ocultar'}</Button>
                        </Box>
                    </React.Fragment>
                }
            </Paper>

            <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
                <form onSubmit={handleSubmit}>
                    <DialogTitle id="form-dialog-title">Registrar Servidor</DialogTitle>
                    <DialogContent>

                        {
                            error.show && <Alert variant="outlined" severity={error.type} style={{ marginBottom: '1rem' }}>{error.msg}</Alert>
                        }

                        <Box display="flex" style={{ width: '100%' }}>
                            <Box flexGrow={1} >
                                <TextField
                                    onChange={handleChange}
                                    id="server-host"
                                    name="host"
                                    label="IP del servidor"
                                    variant="outlined"
                                    size="small"
                                    value={newserver.host}
                                />
                            </Box>
                            <Box>
                                <TextField
                                    onChange={handleChange}
                                    id="server-port"
                                    label="Puerto"
                                    name="port"
                                    type="number"
                                    defaultValue="3128"
                                    inputProps={{ min: 1, max: 65536 }}
                                    InputLabelProps={{
                                        shrink: true,
                                    }}
                                    variant="outlined"
                                    size="small"
                                    value={newserver.port}
                                />
                            </Box>
                        </Box>
                        <Box style={{ width: '100%', marginTop: '1rem' }}>
                            <TextField style={{ width: '100%' }}
                                onChange={handleChange}
                                id="server-password"
                                name="password"
                                label="Contrasea"
                                type="password"
                                autoComplete="current-password"
                                variant="outlined"
                                size="small"
                                value={newserver.password}
                            />
                        </Box>
                    </DialogContent>
                    <DialogActions style={{ justifyContent: 'center', marginBottom: '0.5rem' }}>
                        <div className={classes.wrapper}>
                            <Button
                                type="submit"
                                variant="contained"
                                color="secondary"
                                size="small"
                                className={classes.button}
                                startIcon={<ThumbUpAltIcon />}
                                disabled={loading}
                            >
                                Aceptar
                            </Button>
                            {loading && <CircularProgress size={24} className={classes.buttonProgress} />}
                        </div>
                        <Button
                            onClick={handleClose}
                            variant="contained"
                            color="default"
                            size="small"
                            className={classes.button}
                            startIcon={<CloseIcon />}
                        >
                            Cancelar
                        </Button>
                    </DialogActions>
                </form>
            </Dialog>
        </Grid>
    );
}
