import React, { useState } from "react";
import { makeStyles, withStyles } from '@material-ui/core/styles';
import Badge from '@material-ui/core/Badge';
import CustomGroupRow from "../materialtable/customgrouprow";
import * as Services from "../../services/services";
import { forwardRef } from 'react';
import AddBox from '@material-ui/icons/AddBox';
import Check from '@material-ui/icons/Check';
import ChevronLeft from '@material-ui/icons/ChevronLeft';
import ChevronRight from '@material-ui/icons/ChevronRight';
import Clear from '@material-ui/icons/Clear';
import DeleteOutline from '@material-ui/icons/DeleteOutline';
import Edit from '@material-ui/icons/Edit';
import FilterList from '@material-ui/icons/FilterList';
import FirstPage from '@material-ui/icons/FirstPage';
import LastPage from '@material-ui/icons/LastPage';
import Remove from '@material-ui/icons/Remove';
import SaveAlt from '@material-ui/icons/SaveAlt';
import InfoIcon from '@material-ui/icons/Info';
import HighlightOffIcon from '@material-ui/icons/HighlightOff';
import OpenInNewIcon from '@material-ui/icons/OpenInNew';
import ImportExportIcon from '@material-ui/icons/ImportExport';
import GetAppIcon from '@material-ui/icons/GetApp';
import IconButton from '@material-ui/core/IconButton';
import Search from '@material-ui/icons/Search';
import ViewColumn from '@material-ui/icons/ViewColumn';
import ArrowUpward from "@material-ui/icons/ArrowUpward";
import MaterialTable from "material-table";
import { MuiThemeProvider } from "@material-ui/core/styles";
import Box from '@material-ui/core/Box';
import Typography from '@material-ui/core/Typography';
import Avatar from '@material-ui/core/Avatar';
import DesktopWindowsIcon from '@material-ui/icons/DesktopWindows';
import PersonIcon from '@material-ui/icons/Person';
import Button from '@material-ui/core/Button';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import InputLabel from '@material-ui/core/InputLabel';
import Tooltip from '@material-ui/core/Tooltip';
import Chip from '@material-ui/core/Chip';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Alert from '@material-ui/lab/Alert';
import CircularProgress from '@material-ui/core/CircularProgress';
import CloseIcon from '@material-ui/icons/Close';
import ThumbUpAltIcon from '@material-ui/icons/ThumbUpAlt';
import { green, red, blue, orange, grey } from '@material-ui/core/colors';
import LinearProgress from '@material-ui/core/LinearProgress';
import LdapDialog from "../dialog/ldapdialog";
import ConfirmDialog from "../dialog/confirm";
import SimpleBar from 'simplebar-react';
import 'simplebar/dist/simplebar.min.css';

var validator = require('validator');

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(0),
    textAlign: 'center',
    color: theme.palette.text.secondary,
    borderRadius: '1rem'
  },
  wrapper: {
    margin: theme.spacing(1),
    position: 'relative',
  },
  buttonProgress: {
    color: green[500],
    position: 'absolute',
    top: '50%',
    left: '50%',
    marginTop: -12,
    marginLeft: -12,
  }
}));

const ActiveBadge = withStyles((theme) => ({
  badge: {
    backgroundColor: '#43A047',
    color: '#43A047',
    boxShadow: `0 0 0 2px ${theme.palette.background.paper}`,
    height: '14px',
    minWidth: '14px',
    borderRadius: '1rem',
    '&::after': {
      position: 'absolute',
      top: 0,
      left: 0,
      width: '100%',
      height: '100%',
      borderRadius: '50%',
      animation: '$ripple 1.2s infinite ease-in-out',
      border: '1px solid currentColor',
      content: '""',
    },
  },
  '@keyframes ripple': {
    '0%': {
      transform: 'scale(.8)',
      opacity: 1,
    },
    '100%': {
      transform: 'scale(2.4)',
      opacity: 0,
    },
  },
}))(Badge);

const NoActiveBadge = withStyles((theme) => ({
  badge: {
    backgroundColor: '#495057',
    color: '#495057',
    boxShadow: `0 0 0 2px ${theme.palette.background.paper}`,
    height: '14px',
    minWidth: '14px',
    borderRadius: '1rem',
    '&::after': {
      position: 'absolute',
      top: 0,
      left: 0,
      width: '100%',
      height: '100%',
      borderRadius: '50%',
      border: '1px solid currentColor',
      content: '""',
    },
  },
  '@keyframes ripple': {
    '0%': {
      transform: 'scale(.8)',
      opacity: 1,
    },
    '100%': {
      transform: 'scale(2.4)',
      opacity: 0,
    },
  },
}))(Badge);

export default function Content(props) {
  const classes = useStyles();

  const [source, setSource] = useState(null)

  const [ldapdialog, setLdapDialog] = useState({
    open: false,
    ldapinfo: null,
    groupData: null
  });

  const [locksourcedialog, setLockSourceDialog] = useState({
    open: false,
    groupdata: null
  });

  const [quotadialog, setQuotaDialog] = useState({
    open: false,
    quotas: null,
    quotaid: 'noquota'
  });

  const [uridialog, setURIDialog] = useState({
    open: false,
    rowData: null,
    uriinfo: null
  });

  const [error, setError] = useState({
    show: false,
    type: 'error',
    msg: ''
  });

  const [loading, setLoading] = useState(false);

  const tableIcons = {
    Add: forwardRef((props, ref) => <AddBox {...props} ref={ref} />),
    Check: forwardRef((props, ref) => <Check {...props} ref={ref} />),
    Clear: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
    Delete: forwardRef((props, ref) => <DeleteOutline {...props} ref={ref} />),
    DetailPanel: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
    Edit: forwardRef((props, ref) => <Edit {...props} ref={ref} />),
    Export: forwardRef((props, ref) => <SaveAlt {...props} ref={ref} />),
    Filter: forwardRef((props, ref) => <FilterList {...props} ref={ref} />),
    FirstPage: forwardRef((props, ref) => <FirstPage {...props} ref={ref} />),
    LastPage: forwardRef((props, ref) => <LastPage {...props} ref={ref} />),
    NextPage: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
    PreviousPage: forwardRef((props, ref) => <ChevronLeft {...props} ref={ref} />),
    ResetSearch: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
    Search: forwardRef((props, ref) => <Search {...props} ref={ref} />),
    SortArrow: forwardRef((props, ref) => <ArrowUpward {...props} ref={ref} />),
    ThirdStateCheck: forwardRef((props, ref) => <Remove {...props} ref={ref} />),
    ViewColumn: forwardRef((props, ref) => <ViewColumn {...props} ref={ref} />)
  };

  const processUrl = (rowData) => {
    let text = rowData.uri
    if (rowData.uri.length > 60)
      text = rowData.uri.substring(0, 60) //
    return <Tooltip title={rowData.uri} >
      <Box display="flex" flexDirection='column' alignItems='center'>
        <Typography style={{ maxWidth: '100%', overflow: 'auto' }}>{text}</Typography>
      </Box>
    </Tooltip>
  }

  const logType = (logtype) => {
    let badge = '';
    switch (logtype) {
      case 'TCP_MISS':
        badge = <Chip label="Desde url" color="primary" style={{ height: '1.3rem' }} />;
        break;
      case 'TCP_HIT':
        badge = <Chip label="Desde cach" style={{ height: '1.3rem', background: green[500] }} />;
        break;
      case 'TCP_REFRESH_MODIFIED':
        badge = <Chip label="Refrescado" style={{ height: '1.3rem', background: blue[700] }} />;
        break;
      case 'TCP_DENIED':
        badge = <Chip label="Denegado" style={{ height: '1.3rem', background: red[300] }} />;
        break;
      case 'TCP_TUNNEL':
        badge = <Chip label="Cifrada" style={{ height: '1.3rem', background: orange[800] }} />;
        break;
      case 'TAG_NONE':
        badge = <Chip label="Desconocido" style={{ height: '1.3rem', background: grey[500] }} />;
        break;
      default:
        badge = <Chip label={logtype} style={{ height: '1.3rem' }} />;
        break;
    }
    return badge;
  }

  const delayPools = (delay) => {
    let text = 'Sin lmites';
    if (delay.number !== 0) {
      delay.types.map((type) => {
        if (type.max !== -1) {
          text = 'Limitado a: ' + type.restore + ' despus de: ' + type.max;
        }
        return type
      })
    }
    return text
  }

  const rowToShow = () => {
    if (props.usersConnected > 20)
      return [10, 20, { value: props.usersConnected, label: 'Todo' }]
    else if (props.usersConnected > 10)
      return [10, props.usersConnected]
    return [props.usersConnected]
  }

  const urlInfo = (rowData) => {
    setURIDialog(uridialog => ({ ...uridialog, open: true, rowData: rowData }))
    Services.getUriInfo(rowData.uri).then((response) => {
      if (!response.data.errors && response.data.size !== -1) {
        console.log(response.data)
        setURIDialog(uridialog => ({ ...uridialog, open: true, rowData: rowData, uriinfo: response.data }))
      }
    }).catch((error) => { return })
  }

  const handleClose = () => {
    setSource(null)
    setLdapDialog({
      open: false,
      ldapinfo: null,
      groupData: null
    });
    setLockSourceDialog({
      open: false,
      groupdata: null
    })
    setError({
      show: false,
      type: 'error',
      msg: ''
    })
    setLoading(false)
    setQuotaDialog({
      open: false,
      quotas: null,
      quotaid: 'noquota'
    });
    setURIDialog({
      open: false,
      rowData: null,
      uriinfo: null
    });
  };

  const handleLdapDialog = async (groupData) => {
    if (!validator.isIP(Services.parseSource(groupData.value))) {
      setLdapDialog(ldapdialog => ({ ...ldapdialog, open: true }))
      await Services.getSourceLdapInfo(Services.parseSource(groupData.value), props.server.host).then((response) => {
        setLdapDialog({
          open: true,
          ldapinfo: response.data,
          groupData: groupData
        })
      }).catch((err) => {
        setError(error => ({ ...error, show: true, msg: 'Lo sentimos ha ocurrido un error inesperado.' }))
        setLdapDialog(ldapdialog => ({ ...ldapdialog, open: true }))
      })
    }
  }

  const loadSource = async (source) => {
    await Services.getSourceByIdentifier(source).then((response) => {
      if (!response.data.errors) {
        setSource(response.data);
      }
    }).catch((err) => {
      setError(error => ({ ...error, show: true, msg: 'Lo sentimos ha ocurrido un error inesperado.' }))
    })
  }

  const loadQuotas = async (source) => {
    await Services.getQuotas().then((response) => {
      if (!response.data.errors) {
        setQuotaDialog(quotadialog => ({ ...quotadialog, quotas: response.data }))
      }
    }).catch((err) => {
      setError(error => ({ ...error, show: true, msg: 'Lo sentimos ha ocurrido un error inesperado.' }))
    })
  }

  const handleSourcesLockClick = async (source) => {
    loadSource(source)
    setLockSourceDialog(locksourcedialog => ({ ...locksourcedialog, open: true }));
  }

  const handleLinkQuotaClick = async (source) => {
    console.log('source ', source)
    loadSource(source)
    loadQuotas()
    setQuotaDialog(quotadialog => ({ ...quotadialog, open: true }))
  }

  const handleSourcesLock = async () => {
    setLoading(true)
    await Services.sourceLock(source.identifier, props.server.host).then((response) => {
      setLoading(false)
      if (response.data.errors) {
        props.showMessage(response.data.errors[0].msg, 'error')
        handleClose()
      } else {
        props.showMessage('Se realiz el bloqueo correctamente', 'success')
        handleClose()
        props.handleRefreshSourcesLock()
      }
    }).catch((err) => {
      setLoading(false)
      props.showMessage('Lo sentimos ha ocurrido un error inesperado.', 'error')
      handleClose()
    });
  }

  const handleQuotaChange = (e) => {
    e.preventDefault()
    setQuotaDialog(quotadialog => ({ ...quotadialog, quotaid: e.target.value }))
  }

  const handleSubmit = async () => {
    console.log('origen ', source)
    setError({
      show: false,
      type: 'error',
      msg: ''
    })

    if (!loading) {
      setLoading(true);
    }

    await Services.linkSource({ host: props.server.host, identifier: source.identifier, quotaid: quotadialog.quotaid }).then((response) => {
      setLoading(false);

      if (response.data.errors)
        setError(error => ({ ...error, show: true, msg: response.data.errors[0].msg }))
      else {
        if (response.data.status !== "nothingtodo") {
          props.showMessage(response.data.msg, 'success')
          handleClose()
          props.handleRefreshQuota()
        } else
          setError(error => ({ ...error, show: true, msg: 'Seleccione una cuota de la lista' }))
      }
    }).catch((err) => {
      setLoading(false);
      setError(error => ({ ...error, show: true, msg: 'Lo sentimos ha ocurrido un error inesperado.' }))
    });
  }

  const customRow = rowData => {
    return (
      <tr>
        <td colSpan="9">
          <table style={{ width: '100%' }}>
            <CustomGroupRow handleLinkQuotaClick={handleLinkQuotaClick} handleSourcesLockClick={handleSourcesLockClick} handleLdapDialog={handleLdapDialog} showMessage={props.showMessage} handleRefreshQuota={props.handleRefreshQuota} handleRefreshSourcesLock={props.handleRefreshSourcesLock} {...rowData} />
          </table>
        </td>
      </tr>
    );
  };

  return (
    <MuiThemeProvider>
      <MaterialTable
        style={{ borderRadius: '1rem' }}
        icons={tableIcons}
        title="Detalles de las conexiones"
        data={props.data}
        columns={[
          { title: 'Origen', field: 'username', render: rowData => Services.parseSource(rowData.username), defaultGroupOrder: 0 },
          //{ title: 'Avatar', field: 'imageUrl', render: rowData => <Image src={rowData.imageUrl} width="40" height="40" classname="rounded-circle" /> },
          { title: 'PC', field: 'remote' },
          { title: 'URL', field: 'uri', render: rowData => processUrl(rowData) },
          { title: 'Respuesta', field: 'logType', render: rowData => logType(rowData.logType), grouping: false },//logType
          {
            title: 'Duracin', field: 'time',
            customSort: (a, b) => a.seconds - b.seconds,
            grouping: false
          },
          {
            title: 'Tamao', field: 'size',
            customSort: (a, b) => a.bytes - b.bytes
          },
          {
            title: 'Velocidad', field: 'speed',
            customSort: (a, b) => a.speed_value - b.speed_value
          },
          {
            title: 'Estado', field: 'active', lookup: {
              true: <ActiveBadge
                overlap="circle"
                anchorOrigin={{
                  vertical: 'bottom',
                  horizontal: 'right',
                }}
                variant="dot"
              >
              </ActiveBadge>,
              false: <NoActiveBadge
                overlap="circle"
                anchorOrigin={{
                  vertical: 'bottom',
                  horizontal: 'right',
                }}
                variant="dot"
              >
              </NoActiveBadge>
            }, grouping: false, sorting: false
          }
        ]}

        components={{
          GroupRow: rowData => customRow(rowData)
        }}
        /*onRowClick={((evt, selectedRow: any) => {
        alert(selectedRow.name)
      })}*/
        options={{
          grouping: true,
          sorting: true,
          defaultExpanded: false,
          //exportButton: true,
          paging: true,
          pageSize: 10,
          pageSizeOptions: rowToShow(),
          actionsColumnIndex: -1,
          headerStyle: {
            fontWeight: 700
          },
          rowStyle: {
            padding: '2px',
            border: 'none'
          },
          cellStyle: {
            padding: '2px',
          }
        }}
        localization={{
          grouping: {
            placeholder: 'Arrastrar encabezado aqui para agrupar',
            groupedBy: 'Agrupado por:'
          },
          pagination: {
            labelDisplayedRows: '{from}-{to} de {count}',
            labelRowsSelect: 'registros'
          },
          toolbar: {
            nRowsSelected: '{0} fila(s) seleccionadas',
            exportTitle: 'Exportar',
            exportAriaLabel: 'Exportar',
            exportCSVName: 'Exportar como CSV',
            exportPDFName: 'Exportar como PDF',
            searchTooltip: 'Buscar',
            searchPlaceholder: 'Buscar'
          },
          header: {
            actions: 'Accin'
          },
          body: {
            emptyDataSourceMessage: 'Sin datos que mostrar',
            filterRow: {
              filterTooltip: 'Filter'
            }
          }
        }}
        actions={[
          {
            icon: () => <OpenInNewIcon />,
            tooltip: 'Abrir enlace en una nueva pestaa',
            onClick: (event, rowData) => window.open(rowData.uri, '_blank')
          },
          {
            icon: () => <InfoIcon />,
            tooltip: 'Ver informacin de la uri',
            onClick: (event, rowData) => urlInfo(rowData)
          }
        ]}
      //parentChildData={(row, rows) => rows.find(a => a.id === row.parentId)}
      />

      {
        ldapdialog.ldapinfo === null && !error.show ?
          <Dialog open={ldapdialog.open} onClose={handleClose} aria-labelledby="form-dialog-title">
            {
              <DialogContent>
                <Box display="flex" flexDirection='column' alignItems='center'>
                  <CircularProgress color="secondary" />
                  <Typography variant="h6">Cargando informacin</Typography>
                </Box>
              </DialogContent>

            }
          </Dialog>
          :
          <LdapDialog ldapdialog={ldapdialog} handleClose={handleClose} />
      }


      <Dialog open={uridialog.open} onClose={handleClose} aria-labelledby="form-dialog-title">
        {
          uridialog.rowData ?
            <React.Fragment>
              <DialogContent>
                <Box display="flex" alignItems='center' justifyContent='space-between'>
                  <Typography style={{ display: 'flex', alignItems: 'center' }}><GetAppIcon style={{ fontSize: 30 }} />{uridialog.rowData.size}</Typography>
                  <Typography style={{ display: 'flex', alignItems: 'center' }}><ImportExportIcon style={{ fontSize: 30 }} />{uridialog.rowData.speed}</Typography>
                  <IconButton component="span" onClick={() => { window.open(uridialog.rowData.uri, '_blank') }}>
                    <OpenInNewIcon />
                  </IconButton>
                </Box>

                <SimpleBar style={{ width: '30rem', maxWidth: '100%' }}>
                  <Typography style={{ marginBottom: '10px' }}>{uridialog.rowData.uri}</Typography>
                </SimpleBar>

                <Box>
                  <Typography style={{ display: 'flex', alignItems: 'center', marginTop: '0.5rem' }} variant="body2" color="textSecondary"><HighlightOffIcon style={{ fontSize: 30 }} />{delayPools(uridialog.rowData.delay_pool)}</Typography>
                </Box>

                {
                  uridialog.uriinfo &&
                  <React.Fragment>
                    <Box width="100%" mr={1} mt={1}>
                      <Typography variant="body2" color="textSecondary">Total a descargar {Services.getSize(uridialog.uriinfo.size)}</Typography>
                    </Box>
                    <Box display="flex" alignItems="center">
                      <Box width="100%" mr={1}>
                        <LinearProgress variant="determinate" value={uridialog.rowData.bytes === 0 ? 0 : Math.round(((uridialog.rowData.bytes * 100) / uridialog.uriinfo.size))} />
                      </Box>
                      <Box minWidth={35}>
                        <Typography variant="body2" color="textSecondary">{uridialog.rowData.bytes === 0 ? 0 : Math.round(((uridialog.rowData.bytes * 100) / uridialog.uriinfo.size))}%</Typography>
                      </Box>
                    </Box>
                  </React.Fragment>
                }

              </DialogContent>
              <DialogActions style={{ justifyContent: 'center', marginBottom: '0.5rem' }}>
                <div className={classes.wrapper}>
                  <Button
                    onClick={handleClose}
                    variant="contained"
                    color="secondary"
                    size="small"
                    className={classes.button}
                    startIcon={<ThumbUpAltIcon />}
                    disabled={loading}
                  >
                    Aceptar
                  </Button>
                  {loading && <CircularProgress size={24} className={classes.buttonProgress} />}
                </div>
              </DialogActions>
            </React.Fragment>
            : ''
        }
      </Dialog>


      {
        source === null && error.show &&
        <Dialog open={ldapdialog.open || locksourcedialog.open || quotadialog.open || uridialog.open ? true : false} onClose={handleClose} aria-labelledby="form-dialog-title">
          <DialogTitle id="form-dialog-title">ERROR</DialogTitle>
          <Alert variant="outlined" severity={error.type} style={{ marginBottom: '1rem', marginLeft: '1.6rem', marginRight: '1.6rem' }}>{error.msg}</Alert>
          <DialogActions style={{ justifyContent: 'center', marginBottom: '0.5rem' }}>
            <div className={classes.wrapper}>
              <Button
                onClick={handleClose}
                variant="contained"
                color="secondary"
                size="small"
                className={classes.button}
                startIcon={<ThumbUpAltIcon />}
                disabled={loading}
              >
                Aceptar
              </Button>
              {loading && <CircularProgress size={24} className={classes.buttonProgress} />}
            </div>
          </DialogActions>
        </Dialog>
      }

      {
        source !== null &&
        <ConfirmDialog open={locksourcedialog.open} loading={loading} handleClose={handleClose} handleConfirmClick={handleSourcesLock} >
          <Typography variant="h6" gutterBottom> Seguro que deseas bloquear a:</Typography>
          <Box display="flex" alignItems="center" flexGrow={1} >
            <Avatar>
              {
                validator.isIP(source.identifier) ?
                  <DesktopWindowsIcon /> :
                  <PersonIcon />
              }
            </Avatar>
            <Typography variant="h6" gutterBottom style={{ marginBottom: '0', marginLeft: '10px' }}>
              {Services.parseSource(source.identifier)}
            </Typography>
          </Box>
        </ConfirmDialog>
      }

      {
        source !== null &&
        <Dialog fullWidth={true} maxWidth="xs" open={quotadialog.open} onClose={handleClose} aria-labelledby="form-dialog-title">
          <form onSubmit={handleSubmit}>
            <DialogTitle id="form-dialog-title">Establecer cuota</DialogTitle>
            <DialogContent>

              {
                error.show && <Alert variant="outlined" severity="error" style={{ marginBottom: '1rem' }}>{error.msg}</Alert>
              }

              {
                source !== null && source.quota !== undefined && !source.errors ?
                  <Alert variant="outlined" severity="warning" style={{ marginBottom: '1rem' }}><em>Ya tiene una cuota definida de <b>{Services.getSize(source.quota.limit) + ' - ' + Services.convertQuotaType(source.quota.type)}</b> Seleccione la opcin "Sin cuota" si lo que desea es eliminarla.</em></Alert>
                  : ''
              }

              <Box display="flex" alignItems="center" flexGrow={1} marginBottom="2rem">
                <Avatar>
                  {
                    validator.isIP(source.identifier) ?
                      <DesktopWindowsIcon /> :
                      <PersonIcon />
                  }
                </Avatar>
                <Typography variant="h6" gutterBottom style={{ marginBottom: '0', marginLeft: '10px' }}>
                  {Services.parseSource(source.identifier)}
                </Typography>
              </Box>

              <Box style={{ width: '100%', marginTop: source !== null && source.quota !== undefined ? '2rem' : '1rem' }}>
                <FormControl size="small" variant="outlined" style={{ width: '100%' }}>
                  <InputLabel htmlFor="set-source-quota">Tipo de Cuota</InputLabel>
                  <Select style={{ width: '100%' }}
                    onChange={handleQuotaChange}
                    native
                    label="Tipo de Cuota"
                    inputProps={{
                      name: 'quotaid',
                      id: 'set-source-quota',
                    }}
                  >
                    <option value='noquota'>Sin cuota</option>
                    {
                      quotadialog.quotas !== null && !quotadialog.quotas.errors ?
                        quotadialog.quotas.map((quota, index) => {
                          return <option value={quota._id}>{Services.getSize(quota.limit) + ' - ' + Services.convertQuotaType(quota.type)}</option>
                        })
                        : ''
                    }
                  </Select>
                </FormControl>
              </Box>

            </DialogContent>
            <DialogActions style={{ justifyContent: 'center', marginBottom: '0.5rem' }}>
              <div className={classes.wrapper}>
                <Button
                  onClick={handleSubmit}
                  variant="contained"
                  color="secondary"
                  size="small"
                  className={classes.button}
                  startIcon={<ThumbUpAltIcon />}
                  disabled={loading}
                >
                  Aceptar
                </Button>
                {loading && <CircularProgress size={24} className={classes.buttonProgress} />}
              </div>
              <Button
                onClick={handleClose}
                variant="contained"
                color="default"
                size="small"
                className={classes.button}
                startIcon={<CloseIcon />}
              >
                Cancelar
              </Button>
            </DialogActions>
          </form>
        </Dialog>
      }
    </MuiThemeProvider>
  )
}