import React, { useState } from "react";
import { createMuiTheme, ThemeProvider, makeStyles } from '@material-ui/core/styles';
import Avatar from '@material-ui/core/Avatar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import ConfirmDialog from "../dialog/confirm";
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import AccountTreeOutlinedIcon from '@material-ui/icons/AccountTreeOutlined';
import * as Services from "../../services/services"
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Alert from '@material-ui/lab/Alert';
import CircularProgress from '@material-ui/core/CircularProgress';
import CloseIcon from '@material-ui/icons/Close';
import ThumbUpAltIcon from '@material-ui/icons/ThumbUpAlt';
import TextField from '@material-ui/core/TextField';
import { green } from '@material-ui/core/colors';
import useMediaQuery from '@material-ui/core/useMediaQuery';

const useStyles = makeStyles((theme) => ({
    button: {
        margin: '0',
        minWidth: '0'
    },
    typography: {
        marginBottom: '0',
        marginLeft: '10px'
    },
    rounded: props => ({
        color: props.prefersDarkMode ? theme.palette.text.secondary : theme.palette.secondary,
        backgroundColor: props.prefersDarkMode ? '#343A40' : theme.palette.grey['400']
    }),
    listText: props => ({
        color: props.prefersDarkMode ? theme.palette.text.secondary : theme.palette.secondary
    }),
    wrapper: {
        margin: theme.spacing(1),
        position: 'relative',
    },
    buttonProgress: {
        color: green[500],
        position: 'absolute',
        top: '50%',
        left: '50%',
        marginTop: -12,
        marginLeft: -12,
    }
}));

var validator = require('validator');

export default function ProxyItem(props) {
    const theme = React.useMemo(
        () =>
            createMuiTheme({
                palette: {
                    type: props.prefersDarkMode ? 'dark' : 'light',
                    primary: {
                        light: '#757ce8',
                        main: '#dc3545', //'#3f50b5',
                        dark: '#002884',
                        contrastText: '#fff',
                    },
                    secondary: {
                        light: '#ff7961', //focus
                        main: '#dc3545', //background
                        dark: '#ba000d', //hover
                        contrastText: '#fff', // color
                    },
                    text: {
                        secondary: '#9FA2A5'
                    },
                    background: {
                        paper: props.prefersDarkMode ? '#343A40' : '#fff',
                        default: props.prefersDarkMode ? '#212529' : '#fafafa',
                    }
                },
            }),
        [props.prefersDarkMode],
    );
    const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));

    const classes = useStyles(props);

    const [loading, setLoading] = useState(false);
    const [open, setOpen] = useState(false);

    const [newproxyserver, setNewProxyServer] = useState({
        host: '',
        port: '3128',
        username: '',
        password: ''
    })

    const [error, setError] = useState({
        show: false,
        type: 'error',
        msg: ''
    });

    const handleClose = () => {
        setOpen(false);

        setLoading(false);

        setNewProxyServer({
            host: '',
            port: '3128',
            username: '',
            password: ''
        })

        setError({
            show: false,
            type: 'error',
            msg: ''
        })
    };

    const handleChange = (e) => {
        e.preventDefault()
        setNewProxyServer(newproxyserver => ({ ...newproxyserver, [e.target.name]: e.target.value }));
    }

    const handleSubmit = async (e) => {
        e.preventDefault()
        setError({
            show: false,
            type: 'error',
            msg: ''
        })

        if (!validator.isIP(newproxyserver.host)) {
            setError(error => ({ ...error, show: true, msg: 'La direccin ip del servidor no es vlida' }))
        } else
            if (!validator.isPort(newproxyserver.port.toString())) {
                setError(error => ({ ...error, show: true, msg: 'El puerto no es vlido' }))
            } else
                if (newproxyserver.username.length > 0 && validator.isEmpty(newproxyserver.username, { ignore_whitespace: true })) {
                    setError(error => ({ ...error, show: true, msg: 'El usuario no es vlido' }))
                } else
                    if (newproxyserver.username.length > 0 && validator.isEmpty(newproxyserver.password, { ignore_whitespace: true })) {
                        setError(error => ({ ...error, show: true, msg: 'La contrasea no es vlida' }))
                    } else {
                        if (!loading) {
                            setLoading(true);
                        }

                        await Services.proxyRegister({
                            host: newproxyserver.host,
                            port: newproxyserver.port,
                            username: newproxyserver.username,
                            password: newproxyserver.password
                        }).then((response) => {
                            setLoading(false);
                            if (response.data.errors) {
                                console.log('error', response.data.errors)
                                setError(error => ({ ...error, show: true, msg: response.data.errors[0].msg }))
                            }
                            else {
                                props.showMessage('Proxy registrado correctamente', 'success')
                                handleClose()
                                props.loadConfig();
                            }
                        }).catch((error) => {
                            setLoading(false);
                            setError(error => ({ ...error, show: true, msg: 'Lo sentimos ha ocurrido un error inesperado.' }))
                        });
                    }
    }

    const handleRemoveClick = async () => {
        if (!loading) {
            setLoading(true);
        }
        await Services.deleteProxy(props.proxy._id).then((response) => {
            setLoading(false);
            if (response.data.errors)
                setError(error => ({ ...error, show: true, msg: response.data.errors[0].msg }))
            else {
                handleClose()
                props.showMessage('Proxy eliminado correctamente', 'success')
                props.loadConfig();
            }
        }).catch((error) => {
            setLoading(false);
            setError(error => ({ ...error, show: true, msg: 'Lo sentimos ha ocurrido un error inesperado.' }))
        })
    }

    return (<React.Fragment>
        <ListItem onClick={(e) => { e.preventDefault(e); setOpen(true) }} button>
            <ListItemAvatar>
                <Avatar variant="rounded" className={classes.rounded}>
                    <AccountTreeOutlinedIcon />
                </Avatar>
            </ListItemAvatar>
            <ListItemText primary="Proxy" className={classes.listText} />
        </ListItem>
        <ThemeProvider theme={theme}>
            {
                props.proxy === null ?
                    <Dialog fullScreen={fullScreen} open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
                        <form onSubmit={handleSubmit}>
                            <DialogTitle id="form-dialog-title">Configurar proxy</DialogTitle>
                            <DialogContent>
                                {
                                    error.show && <Alert variant="outlined" severity={error.type} style={{ marginBottom: '1rem' }}>{error.msg}</Alert>
                                }

                                <Box display="flex" style={{ width: '100%' }}>
                                    <Box flexGrow={1} style={{ width: '100%' }}>
                                        <TextField style={{ width: '100%' }}
                                            onChange={handleChange}
                                            id="serverproxy-host"
                                            name="host"
                                            label="IP del servidor"
                                            variant="outlined"
                                            size="small"
                                            value={newproxyserver.host}
                                        />
                                    </Box>
                                    <Box>
                                        <TextField
                                            onChange={handleChange}
                                            id="serverproxy-port"
                                            label="Puerto"
                                            name="port"
                                            type="number"
                                            defaultValue="3128"
                                            inputProps={{ min: 1, max: 65536 }}
                                            InputLabelProps={{
                                                shrink: true,
                                            }}
                                            variant="outlined"
                                            size="small"
                                            value={newproxyserver.port}
                                        />
                                    </Box>
                                </Box>
                                <Box display="flex" style={{ width: '100%', marginTop: '1rem', justifyContent: 'center' }}>
                                    <small>Campos opcionales</small>
                                </Box>
                                <Box display="flex" style={{ width: '100%', marginTop: '1rem' }}>
                                    <Box flexGrow={1}>
                                        <TextField style={{ width: '100%' }}
                                            onChange={handleChange}
                                            id="serverproxy-username"
                                            name="username"
                                            label="Usuario"
                                            variant="outlined"
                                            size="small"
                                            value={newproxyserver.username}
                                        />
                                    </Box>
                                    <Box >
                                        <TextField style={{ width: '100%' }}
                                            onChange={handleChange}
                                            id="serverproxy-password"
                                            name="password"
                                            label="Contrasea"
                                            type="password"
                                            autoComplete="current-password"
                                            variant="outlined"
                                            size="small"
                                            value={newproxyserver.password}
                                        />
                                    </Box>
                                </Box>


                            </DialogContent>
                            <DialogActions style={{ justifyContent: 'center', marginBottom: '0.5rem' }}>
                                <div className={classes.wrapper}>
                                    <Button
                                        type="submit"
                                        variant="contained"
                                        color="secondary"
                                        size="small"
                                        className={classes.button}
                                        startIcon={<ThumbUpAltIcon />}
                                        disabled={loading}
                                    >
                                        Aceptar
                            </Button>
                                    {loading && <CircularProgress size={24} className={classes.buttonProgress} />}
                                </div>
                                <Button
                                    onClick={handleClose}
                                    variant="contained"
                                    color="default"
                                    size="small"
                                    className={classes.button}
                                    startIcon={<CloseIcon />}
                                >
                                    Cancelar
                        </Button>
                            </DialogActions>
                        </form>
                    </Dialog>
                    : <ConfirmDialog open={open} loading={loading} handleClose={handleClose} handleConfirmClick={handleRemoveClick} >
                        <Typography variant="h6" gutterBottom> Ya existe un proxy registrado desea eliminarlo de forma permanente:</Typography>

                        <Alert variant="outlined" severity={error.type} style={{ marginBottom: '1rem' }}>Tenga en cuenta que si su conexin es a travez de este proxy el bot podra dejar de funcionar al igual que no se podr calcular el progreso de las descargas</Alert>

                        <Box display="flex" alignItems="center" flexGrow={1} >
                            <Typography variant="h6" gutterBottom className={classes.typography}>
                                {props.proxy.uri /*(Services.extractProxyData(props.proxy.uri)).host*/}
                            </Typography>
                        </Box>
                    </ConfirmDialog>
            }
        </ThemeProvider>
    </React.Fragment>);
}
