import React, { useState } from "react";
import { makeStyles } from '@material-ui/core/styles';
import Avatar from '@material-ui/core/Avatar';
import Typography from '@material-ui/core/Typography';
import PersonIcon from '@material-ui/icons/Person';
import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import DeleteOutlineIcon from '@material-ui/icons/DeleteOutline';
import ConfirmDialog from "../dialog/confirm";
import * as Services from "../../services/services"

const useStyles = makeStyles((theme) => ({
    button: {
        margin: '0',
        minWidth: '0'
    },
    typography: {
        marginBottom: '0',
        marginLeft: '10px'
    }
}));

export default function TelegramChatItem(props) {
    const classes = useStyles();

    const [loading, setLoading] = useState(false);
    const [open, setOpen] = useState(false);

    const handleRemoveClick = async () => {
        if (!loading) {
            setLoading(true);
        }
        await Services.deleteChat(props.chat.id).then((response) => {
            setLoading(false);
            if (response.data.errors)
                props.showMessage(response.data.errors[0].msg, 'error')
            else {
                props.showMessage('Usuario ' + props.chat.name + ' eliminado correctamente', 'success')
                handleClose()
                props.loadChats();
            }
        }).catch((error) => {
            props.showMessage('Lo sentimos ha ocurrido un error inesperado.', 'error')
        })

        /*await Services.deleteServer(props.server._id).then((response) => {
            setLoading(false);
            if (response.data.errors)
                props.showMessage(response.data.errors[0].msg, 'error')
            else {
                props.showMessage('Servidor ' + response.data.host + ' eliminado correctamente', 'success')
                props.loadServers()
            }
        }).catch((error) => {
            setLoading(false);
            props.showMessage('No se pudo eliminar el servidor la respuesta fue: ' + error.message, 'error')
        })*/
    }

    const handleClose = () => {
        setOpen(false);
        setLoading(false);
    };

    return (
        <Box display="flex" alignItems="center" flexWrap="wrap" p={1} bgcolor="background.paper">
            <Box display="flex" alignItems="center" flexGrow={1} >
                <Avatar>
                    <PersonIcon fontSize="medium" />
                </Avatar>
                <Box display="flex" flexGrow={1} flexDirection='column'>
                    <Typography color="textPrimary" gutterBottom className={classes.typography}>
                        {props.chat.name}
                    </Typography>
                    <Typography gutterBottom className={classes.typography}>
                        <em>{props.chat.id}</em>
                    </Typography>
                </Box>
            </Box>
            <Button onClick={(e) => setOpen(true)} variant="contained" size="small" color="secondary" className={classes.button}>
                <DeleteOutlineIcon />
            </Button>
            <ConfirmDialog open={open} loading={loading} handleClose={handleClose} handleConfirmClick={handleRemoveClick} >
                <Typography variant="h6" gutterBottom> Esta a punto de eliminar el usuario del bot:</Typography>
                <Box display="flex" alignItems="center" flexGrow={1} >
                    <Avatar>
                        <PersonIcon fontSize="medium" />
                    </Avatar> 
                    <Box display="flex" flexGrow={1} flexDirection='column'>
                        <Typography gutterBottom className={classes.typography}>
                            {props.chat.name}
                        </Typography>
                        <Typography gutterBottom className={classes.typography}>
                            {props.chat.id}
                        </Typography>
                    </Box>
                </Box>
            </ConfirmDialog>
        </Box>
    );
}
