import React from "react";
import { createMuiTheme, ThemeProvider, makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Chip from '@material-ui/core/Chip';
import SSHItem from "./sshitem"
import StatusItem from "./statusitem"
import RestartItem from "./restartitem"
import LinkLdapItem from "./linkldapitem"
import RoutesItem from "./routesitem"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

const useStyles = makeStyles((theme) => ({
  rootChip: props => ({
    background: props.serverIsUp ? '#43A047' : '#dc3545',
    marginRight: '2px'
  }),
  margin: {
    margin: theme.spacing(0.3),
  },
  flex: {
    alignItems: 'center',
    justifyContent: 'center',
    display: 'flex',
    flexWrap: 'wrap'
  },
  server: {
    marginRight: theme.spacing(1),
    fontSize: '20px',
  },
  span: props => ({
    color: '#FFFFFF',
    background: props.serverIsUp ? '#43A047' : '#dc3545',
    borderRadius: '2rem',
    paddingLeft: '5px',
    paddingRight: '5px',
    marginRight: '5px',
  })
}));

export default function ServerOptions(props) {
  const classes = useStyles(props);

  const theme = React.useMemo(
    () =>
      createMuiTheme({
        palette: {
          type: props.prefersDarkMode ? 'dark' : 'light',
          primary: {
            light: '#757ce8',
            main: '#dc3545', //'#3f50b5',
            dark: '#002884',
            contrastText: '#fff',
          },
          secondary: {
            light: '#ff7961', //focus
            main: '#dc3545', //background
            dark: '#ba000d', //hover
            contrastText: '#fff', // color
          },
          text: {
            secondary: '#9FA2A5'
          },
          background: {
            paper: props.prefersDarkMode ? '#343A40' : '#fff',
            default: props.prefersDarkMode ? '#212529' : '#fafafa',
          }
        },
      }),
    [props.prefersDarkMode],
  );

  return (
    <ThemeProvider theme={theme}>
      {
        props.server.host !== undefined ?
          <div className={classes.flex}>
            <Chip
              classes={{
                root: classes.rootChip
              }}
              label={
                props.serverIsUp ?
                  <FontAwesomeIcon icon="arrow-up" size='sm' />
                  :
                  <FontAwesomeIcon icon="arrow-down" size='sm' />
              } style={{ background: "success" }} />

            <Typography variant='h5' style={{ marginRight: '5px' }}>{props.server.host}</Typography>

            <div className={classes.flex}>
              <SSHItem server={props.server} showMessage={props.showMessage} />

              <RestartItem server={props.server} showMessage={props.showMessage} />

              <StatusItem server={props.server} showMessage={props.showMessage} />

              <RoutesItem server={props.server} showMessage={props.showMessage} />

              <LinkLdapItem server={props.server} showMessage={props.showMessage} />

            </div>
          </div>
          : ""
      }
    </ThemeProvider>
  )
}