
import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import Paper from '@material-ui/core/Paper';
import Container from '@material-ui/core/Container';

import Graphic from '../graphic/graphic'
import * as Services from '../../services/services'

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    paper: {
        padding: theme.spacing(0),
        textAlign: 'center',
        color: theme.palette.text.secondary,
        borderRadius: '1rem'
    },
    listTextprimary: {
        fontSize: '1.25rem',
        fontWeight: '700'
    }
}));

export default function ResumeItem(props) {
    const classes = useStyles();

    const getText = () => {
        let text = props.data.length > 0 ? props.data[props.data.length - 1] : 0
        if (props.type === 'size')
            text = Services.getSize(text)
        else if (props.type === 'speed')
            text = Services.getSize(text) + '/s'
        return text
    }

    return (
        <Paper className={classes.paper}>
            <div className={classes.demo}>
                <List >
                    <ListItem>
                        <ListItemText className={classes.listTextprimary}
                            primary={getText()} secondary={props.text}
                            primaryTypographyProps={{style: {fontSize:'1.5rem', color: props.prefersDarkMode ? '#fff' : '#212529'}}}
                            secondaryTypographyProps={{style: {fontSize:'1.5rem', color: props.prefersDarkMode ? 'inherit' : 'gray'}}}
                        />
                        <ListItemSecondaryAction>
                            { props.icon }
                        </ListItemSecondaryAction>
                    </ListItem>
                </List>
            </div>
            <Container>
                <Graphic type={props.type} data={props.data} id={props.graphicid} graphicheight={80} />
            </Container>
        </Paper>

    );
}
