import React, { useState } from "react";
import { createMuiTheme, ThemeProvider, makeStyles } from '@material-ui/core/styles';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import SettingsInputComponentIcon from '@material-ui/icons/SettingsInputComponent';
import * as Services from "../../services/services"
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Alert from '@material-ui/lab/Alert';
import CircularProgress from '@material-ui/core/CircularProgress';
import CloseIcon from '@material-ui/icons/Close';
import ThumbUpAltIcon from '@material-ui/icons/ThumbUpAlt';
import TextField from '@material-ui/core/TextField';
import { green } from '@material-ui/core/colors';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import ReactTagInput from "@pathofdev/react-tag-input";
import "@pathofdev/react-tag-input/build/index.css";

const useStyles = makeStyles((theme) => ({
    button: {
        margin: '0',
        minWidth: '0'
    },
    typography: {
        marginBottom: '0',
        marginLeft: '10px'
    },
    rounded: props => ({
        color: props.prefersDarkMode ? theme.palette.text.secondary : theme.palette.secondary,
        backgroundColor: props.prefersDarkMode ? '#343A40' : theme.palette.grey['400']
    }),
    listText: props => ({
        color: props.prefersDarkMode ? theme.palette.text.secondary : theme.palette.secondary
    }),
    wrapper: {
        margin: theme.spacing(1),
        position: 'relative',
    },
    buttonProgress: {
        color: green[500],
        position: 'absolute',
        top: '50%',
        left: '50%',
        marginTop: -12,
        marginLeft: -12,
    }
}));

var validator = require('validator');

export default function UserNotifyConnection(props) {
    const theme = React.useMemo(
        () =>
            createMuiTheme({
                palette: {
                    type: props.prefersDarkMode ? 'dark' : 'light',
                    primary: {
                        light: '#757ce8',
                        main: '#dc3545', //'#3f50b5',
                        dark: '#002884',
                        contrastText: '#fff',
                    },
                    secondary: {
                        light: '#ff7961', //focus
                        main: '#dc3545', //background
                        dark: '#ba000d', //hover
                        contrastText: '#fff', // color
                    },
                    text: {
                        secondary: '#9FA2A5'
                    },
                    background: {
                        paper: props.prefersDarkMode ? '#343A40' : '#fff',
                        default: props.prefersDarkMode ? '#212529' : '#fafafa',
                    }
                },
            }),
        [props.prefersDarkMode],
    );
    const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));

    const classes = useStyles(props);

    const [loading, setLoading] = useState(false);
    const [open, setOpen] = useState(false);

    const [newnotify, setNewNotify] = useState({
        identifier: '',
        sites: []
    })

    const [error, setError] = useState({
        show: false,
        type: 'error',
        msg: ''
    });

    const handleClose = () => {
        setOpen(false);

        setLoading(false);

        setNewNotify({
            identifier: '',
            sites: []
        })

        setError({
            show: false,
            type: 'error',
            msg: ''
        })
    };

    const handleChange = (e) => {
        e.preventDefault()
        setNewNotify(newnotify => ({ ...newnotify, [e.target.name]: e.target.value }));
    }

    const handleSubmit = async (e) => {
        e.preventDefault()
        setError({
            show: false,
            type: 'error',
            msg: ''
        })

        /*let sites = newnotify.sites.split(',');
        let siteisvalid = true;
        sites.map((site) => {
            if (validator.isEmpty(site, { ignore_whitespace: true })) {
                siteisvalid = false;
            }
            return site
        })*/

        if (validator.isEmpty(newnotify.identifier, { ignore_whitespace: true })) {
            setError(error => ({ ...error, show: true, msg: 'El usuario o la direccin ip no es vlida' }))
        } else
            if (newnotify.sites.length === 0) {
                setError(error => ({ ...error, show: true, msg: 'Debe especificar al menos un sitio o palabra' }))
            } else {
                if (!loading) {
                    setLoading(true);
                }
                await Services.connectionToSite({
                    host: props.server.host,
                    identifier: newnotify.identifier,
                    sites: newnotify.sites
                }).then((response) => {
                    setLoading(false);
                    if (response.data.errors) {
                        setError(error => ({ ...error, show: true, msg: response.data.errors[0].msg }))
                    }
                    else {
                        props.showMessage('Notificacin resgistrada correctamente', 'success')
                        handleClose()
                    }
                }).catch((err) => {
                    setLoading(false);
                    setError(error => ({ ...error, show: true, msg: 'Lo sentimos ha ocurrido un error inesperado.' }))
                });
            }
    }

    /*useEffect(() => {
        var reactTagInput = document.querySelector(".react-tag-input__input");
        reactTagInput.addEventListener("focus", ()=>{
            reactTagInput.classList.add("react-tag-input-focus");
        }, true);
        reactTagInput.addEventListener("blur", ()=>{
            reactTagInput.classList.remove("react-tag-input-focus");
        }, true);
    }, [])*/

    return (<React.Fragment>
        <ListItem onClick={(e) => { e.preventDefault(e); setOpen(true) }} button>
            <ListItemAvatar>
                <Avatar variant="rounded" className={classes.rounded}>
                    <SettingsInputComponentIcon />
                </Avatar>
            </ListItemAvatar>
            <ListItemText primary="Conexin a sitios" className={classes.listText} />
        </ListItem>
        <ThemeProvider theme={theme}>
            {

                <Dialog fullScreen={fullScreen} fullWidth={true} maxWidth="xs" open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
                    <form onSubmit={handleSubmit}>
                        <DialogTitle id="form-dialog-title">Conexin a sitios</DialogTitle>
                        <DialogContent>
                            {
                                error.show && <Alert variant="outlined" severity={error.type} style={{ marginBottom: '1rem' }}>{error.msg}</Alert>
                            }

                            <Box style={{ width: '100%' }}>
                                <TextField style={{ width: '100%' }}
                                    onChange={handleChange}
                                    name="identifier"
                                    label="Usuario o IP"
                                    variant="outlined"
                                    size="small"
                                    value={newnotify.identifier}
                                />
                            </Box>
                            <Box style={{ width: '100%', marginTop: '1rem' }}>
                                {/*<TextField style={{ width: '100%' }}
                                    onChange={handleChange}
                                    id="servermail-to"
                                    name="sites"
                                    label="Sitios"
                                    multiline
                                    rows={3}
                                    variant="outlined"
                                    size="small"
                                    placeholder="Separadas por coma (,)"
                                    value={newnotify.sites}
                        />*/}
                                <ReactTagInput
                                    tags={newnotify.sites}
                                    name="to"
                                    placeholder="Sitios o palabras. Precione enter al escribir"
                                    editable={true}
                                    readOnly={false}
                                    removeOnBackspace={true}
                                    onChange={(toSiteAddress) => { setNewNotify(newnotify => ({ ...newnotify, sites: toSiteAddress }));/*setTags(newTags)*/ }}
                                    validator={(value) => {
                                        let isValid = true
                                        let sites = value.split(' ');
                                        if (sites.length > 1) {
                                            isValid = false
                                            setError(error => ({ ...error, show: true, msg: 'El sitio o palabra no debe de contener espacios' }))
                                        }
                                        if (isValid) {
                                            setError(error => ({ ...error, show: false, msg: '' }))
                                        }
                                        return isValid;
                                    }}
                                />
                            </Box>
                        </DialogContent>
                        <DialogActions style={{ justifyContent: 'center', marginBottom: '0.5rem' }}>
                            <div className={classes.wrapper}>
                                <Button
                                    type="submit"
                                    variant="contained"
                                    color="secondary"
                                    size="small"
                                    className={classes.button}
                                    startIcon={<ThumbUpAltIcon />}
                                    disabled={loading}
                                >
                                    Aceptar
                                </Button>
                                {loading && <CircularProgress size={24} className={classes.buttonProgress} />}
                            </div>
                            <Button
                                onClick={handleClose}
                                variant="contained"
                                color="default"
                                size="small"
                                className={classes.button}
                                startIcon={<CloseIcon />}
                            >
                                Cancelar
                            </Button>
                        </DialogActions>
                    </form>
                </Dialog>
            }
        </ThemeProvider>
    </React.Fragment>);
}
