import React, { useState } from "react";
import { makeStyles } from '@material-ui/core/styles';
import Avatar from '@material-ui/core/Avatar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import PersonIcon from '@material-ui/icons/Person';
import DesktopWindowsIcon from '@material-ui/icons/DesktopWindows';
import LockOpenIcon from '@material-ui/icons/LockOpen';
import Moment from 'react-moment';
import 'moment/locale/es';

import * as Services from "../../services/services"
import ConfirmDialog from "../dialog/confirm";

const useStyles = makeStyles((theme) => ({
    button: {
        margin: '0',
        minWidth: '0'
    },
    typography: {
        marginBottom: '0',
        marginLeft: '10px'
    },
    small: {
        background: '#dc3545',
        borderRadius: '15px',
        color: 'white'
    }
}));

export default function LockedSource(props) {
    const classes = useStyles();

    const [loading, setLoading] = useState(false);
    const [open, setOpen] = useState(false);

    const handleUnlockClick = async () => {
        if (!loading) {
            setLoading(true);
        }
        await Services.sourceUnlock(props.sourcelock.identifier, props.server.host).then((response) => {
            setLoading(false);
            if (response.data.errors)
                props.showMessage(response.data.errors[0].msg, 'error')
            else {
                props.showMessage('Se realiz el desbloqueo correctamente', 'success')
                handleClose()
                props.refreshSourcesLock(props.sourcelock.identifier)
            }
        }).catch((error) => {
            setLoading(false);
            props.showMessage('Lo sentimos ha ocurrido un error inesperado.', 'error')
        })
    }

    const handleClose = () => {
        setOpen(false);
        setLoading(false);
    };

    return (
        <Box display="flex" alignItems="center" flexWrap="wrap" p={1} bgcolor="background.paper">
            <Box display="flex" alignItems="center" flexGrow={1} >
                <Avatar>
                    {
                        Services.isIPAddress(props.sourcelock.identifier) ?
                            <DesktopWindowsIcon fontSize="medium" /> :
                            <PersonIcon fontSize="medium" />
                    }
                </Avatar>
                <Box display="flex" flexGrow={1} flexDirection='column'>
                    <Typography color="textPrimary" gutterBottom className={classes.typography} display='flex'>
                        {Services.isIPAddress(props.sourcelock.identifier) ? props.sourcelock.identifier : Services.parseSource(props.sourcelock.identifier)} <small className={classes.small}><em style={{ margin: '5px' }}>{props.sourcelock.lock_from ===  'file' ? 'archivo' : props.sourcelock.lock_from}</em></small>
                    </Typography>
                    <Typography gutterBottom className={classes.typography}>
                    <em><Moment locale="es" fromNow>{props.sourcelock.lock_date}</Moment></em>
                    </Typography>
                </Box>
            </Box>
            <Button onClick={(e) => { e.preventDefault(); setOpen(true) }} variant="contained" size="small" color="secondary" className={classes.button}>
                <LockOpenIcon />
            </Button>

            <ConfirmDialog open={open} loading={loading} handleClose={handleClose} handleConfirmClick={handleUnlockClick} >
                <Typography variant="h6" gutterBottom> Esta a punto de desbloquear:</Typography>
                <Box display="flex" alignItems="center" flexGrow={1} >
                    <Avatar>
                        {
                            Services.isIPAddress(props.sourcelock.identifier) ?
                                <DesktopWindowsIcon fontSize="medium" /> :
                                <PersonIcon fontSize="medium" />
                        }
                    </Avatar>
                    <Box display="flex" flexGrow={1} flexDirection='column'>
                        <Typography gutterBottom className={classes.typography} display='flex'>
                            {Services.isIPAddress(props.sourcelock.identifier) ? props.sourcelock.identifier : Services.parseSource(props.sourcelock.identifier)} <small className={classes.small}><em style={{ margin: '5px' }}>{props.sourcelock.lock_from ===  'file' ? 'archivo' : props.sourcelock.lock_from}</em></small>
                        </Typography>
                        <Typography gutterBottom className={classes.typography}>
                        <em><Moment locale="es" fromNow>{props.sourcelock.lock_date}</Moment></em>
                        </Typography>
                    </Box>
                </Box>
            </ConfirmDialog>
        </Box>
    );
}
