import React, { useState } from "react";
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import StorageIcon from '@material-ui/icons/Storage';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import InputLabel from '@material-ui/core/InputLabel';
import * as Services from "../../services/services"
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Alert from '@material-ui/lab/Alert';
import CircularProgress from '@material-ui/core/CircularProgress';
import CloseIcon from '@material-ui/icons/Close';
import ThumbUpAltIcon from '@material-ui/icons/ThumbUpAlt';
import { green } from '@material-ui/core/colors';

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
    margin: {
        margin: theme.spacing(0.3),
    },
    button: {
        margin: '0',
        minWidth: '0'
    },
    typography: {
        marginBottom: '0',
        marginLeft: '10px'
    },
    rounded: props => ({
        color: props.prefersDarkMode ? theme.palette.text.secondary : theme.palette.secondary,
        backgroundColor: props.prefersDarkMode ? '#343A40' : theme.palette.grey['400']
    }),
    listText: props => ({
        color: props.prefersDarkMode ? theme.palette.text.secondary : theme.palette.secondary
    }),
    wrapper: {
        margin: theme.spacing(1),
        position: 'relative',
    },
    buttonProgress: {
        color: green[500],
        position: 'absolute',
        top: '50%',
        left: '50%',
        marginTop: -12,
        marginLeft: -12,
    }
}));

export default function LinkLdapItem(props) {
    const classes = useStyles(props);

    const [loading, setLoading] = useState(false);
    const [open, setOpen] = useState(false);

    const [server, setServer] = useState(null);
    const [ldapserverid, setLdapServerId] = useState('noldapserver')
    const [ldapservers, setLdapServers] = useState([])

    const [error, setError] = useState({
        show: false,
        type: 'error',
        msg: ''
    });

    const handleClose = () => {

        setOpen(false);

        setError({
            show: false,
            type: 'error',
            msg: ''
        })

        setLoading(false);

        setServer(null)

        setLdapServerId('noldapserver')
    };

    const handleChange = (e) => {
        e.preventDefault()
        setLdapServerId(e.target.value);
    }

    const handleSubmit = async () => {
        setError({
            show: false,
            type: 'error',
            msg: ''
        })

        if (!loading) {
            setLoading(true);
        }

        await Services.linkProxyServer({ proxyid: server._id, ldapid: ldapserverid }).then((response) => {
            setLoading(false);

            if (response.data.errors)
                setError(error => ({ ...error, show: true, msg: response.data.errors[0].msg }))
            else {
                if (response.data.status !== "nothingtodo") {
                    props.showMessage(response.data.msg, 'success')
                    handleClose()
                } else
                    setError(error => ({ ...error, show: true, msg: 'Seleccione un servidor LDAP de la lista' }))
            }
        }).catch((err) => {
            setLoading(false);
            setError(error => ({ ...error, show: true, msg: 'Lo sentimos ha ocurrido un error inesperado.'}))
        });
    }

    const loadServersData = async () => {
        await Services.getServersById(props.server._id).then((response) => {
            if (!response.data.errors) {
                setServer(response.data)
            }
        }).catch((err) => {
            setLoading(false);
            setError(error => ({ ...error, show: true, msg: 'Lo sentimos ha ocurrido un error inesperado.' }))
        })
        await Services.getLdapServers().then((response) => {
            if (!response.data.errors) {
                setLdapServers(response.data)
            }
        }).catch((err) => {
            setLoading(false);
            setError(error => ({ ...error, show: true, msg: 'Lo sentimos ha ocurrido un error inesperado.' }))
        })
    }

    return (<React.Fragment>
        <Button onClick={(e) => { e.preventDefault(); loadServersData(); setOpen(true) }} variant="contained" color="secondary" size="small" className={classes.margin}
            startIcon={<StorageIcon />}>
            LDAP
        </Button>

        {
            server == null ?
                error.show &&
                <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
                    <DialogTitle id="form-dialog-title">ERROR</DialogTitle>
                    <Alert variant="outlined" severity={error.type} style={{ marginBottom: '1rem', marginLeft: '1.6rem', marginRight: '1.6rem' }}>{error.msg}</Alert>
                    <DialogActions style={{ justifyContent: 'center', marginBottom: '0.5rem' }}>
                        <div className={classes.wrapper}>
                            <Button
                                onClick={handleClose}
                                variant="contained"
                                color="secondary"
                                size="small"
                                className={classes.button}
                                startIcon={<ThumbUpAltIcon />}
                                disabled={loading}
                            >
                                Aceptar
                            </Button>
                            {loading && <CircularProgress size={24} className={classes.buttonProgress} />}
                        </div>
                    </DialogActions>
                </Dialog>
                :
                <Dialog fullWidth={true} maxWidth="xs" open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
                    <form onSubmit={handleSubmit}>
                        <DialogTitle id="form-dialog-title">Vincular a servidor LDAP</DialogTitle>
                        <DialogContent>

                            {
                                error.show && <Alert variant="outlined" severity={error.type} style={{ marginBottom: '1rem' }}>{error.msg}</Alert>
                            }

                            {
                                server.serverldap !== undefined && !server.errors ?
                                    <Alert variant="outlined" severity="warning" style={{ marginBottom: '1rem' }}><em>Este servidor proxy esta vinculado al servidor LDAP <b>{server.serverldap.host}</b>. Seleccione la opcin "Sin servidor ldap vinculado" si lo que desea es eliminar dicho vinculo.</em></Alert>
                                    : ''
                            }

                            <Box style={{ width: '100%', marginTop: server !== null && server.serverldap !== undefined ? '2rem' : '1rem' }}>
                                <FormControl size="small" variant="outlined" style={{ width: '100%' }}>
                                    <InputLabel htmlFor="set-ldap-server-link">Servidores LDAP</InputLabel>
                                    <Select style={{ width: '100%' }}
                                        onChange={handleChange}
                                        native
                                        label="Servidores LDAP"
                                        inputProps={{
                                            name: 'ldap-server-link',
                                            id: 'set-ldap-server-link',
                                        }}
                                    >
                                        <option value='noldapserver'>Sin servidor ldap vinculado</option>
                                        {
                                            ldapservers.map((ldapserver, index) => {
                                                return <option value={ldapserver._id}>{ldapserver.host}</option>
                                            })
                                        }
                                    </Select>
                                </FormControl>
                            </Box>

                        </DialogContent>
                        <DialogActions style={{ justifyContent: 'center', marginBottom: '0.5rem' }}>
                            <div className={classes.wrapper}>
                                <Button
                                    onClick={handleSubmit}
                                    variant="contained"
                                    color="secondary"
                                    size="small"
                                    className={classes.button}
                                    startIcon={<ThumbUpAltIcon />}
                                    disabled={loading}
                                >
                                    Aceptar
                        </Button>
                                {loading && <CircularProgress size={24} className={classes.buttonProgress} />}
                            </div>
                            <Button
                                onClick={handleClose}
                                variant="contained"
                                color="default"
                                size="small"
                                className={classes.button}
                                startIcon={<CloseIcon />}
                            >
                                Cancelar
                        </Button>
                        </DialogActions>
                    </form>
                </Dialog>
        }


    </React.Fragment >);
}
