import React, { useState } from "react";
import { createMuiTheme, ThemeProvider, makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import Hidden from '@material-ui/core/Hidden';
import Brightness6Icon from '@material-ui/icons/Brightness6';
import InfoIcon from '@material-ui/icons/Info';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import Alert from '@material-ui/lab/Alert';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import Logo from '../logo/logo'
import ServerOptions from '../serveroptions/serveroptions'

const useStyles = makeStyles((theme) => ({
  appBar: {
    [theme.breakpoints.up('lg')]: {
      width: `100%`,//calc(100% - ${drawerWidth}px)
      marginLeft: 230,
    },
    zIndex: theme.zIndex.drawer + 1,
    boxShadow: 'none'
  },
  menuButton: {
    [theme.breakpoints.up('lg')]: {
      display: 'none',
    }
  },
  grow: {
    flexGrow: 1,
  },
  sectionDesktop: {
    display: 'flex',
  },
  margin: {
    marginLeft: '11.1rem'
  },
  marginSmall: {
    marginLeft: '3rem'
  }
}))

export default function Header(props) {
  const classes = useStyles();
  const theme = React.useMemo(
    () =>
      createMuiTheme({
        palette: {
          type: props.prefersDarkMode ? 'dark' : 'light',
          primary: {
            light: '#757ce8',
            main: '#dc3545', //'#3f50b5',
            dark: '#002884',
            contrastText: '#fff',
          },
          secondary: {
            light: '#ff7961', //focus
            main: '#dc3545', //background
            dark: '#ba000d', //hover
            contrastText: '#fff', // color
          },
          text: {
            secondary: '#9FA2A5'
          },
          background: {
            paper: props.prefersDarkMode ? '#212529' : '#e9ecef',
            default: props.prefersDarkMode ? '#212529' : '#fafafa'
          }
        },
      }),
    [props.prefersDarkMode],
  );

  const logout = () => {
    sessionStorage.removeItem('token');
    props.setToken('')
  }

  const [subscribe, setSubscribe] = useState(true)
  const [open, setOpen] = useState(false)
  const [error, setError] = useState({
    show: false,
    type: 'error',
    msg: ''
  });

  const handleChange = (e) => {
    e.preventDefault()
    props.handlePrefersDarkMode()
  };

  const handleSubscriptions = (e) => {
    e.preventDefault()
    if (subscribe) {
      props.socket.disconnect()
    } else {
      props.socket.connect()
    }
    setSubscribe(!subscribe)
  }

  const handleClose = () => {
    setOpen(false)
    setError({
      show: false,
      type: 'error',
      msg: ''
    })
  };

  return (
    <ThemeProvider theme={theme}>
      <AppBar color="inherit" position="fixed" className={classes.appBar}>
        <Toolbar>
          <Logo></Logo>
          <Hidden mdDown implementation="css">
            <div className={classes.margin}>
            </div>
          </Hidden>
          <Hidden lgUp implementation="css">
            <div className={classes.marginSmall}>
            </div>
          </Hidden>
          <Hidden smDown implementation="css">
            {
              props.server !== undefined ? <ServerOptions handleRefreshSourcesLock={props.handleRefreshSourcesLock} showMessage={props.showMessage} server={props.server} serverIsUp={props.serverIsUp} prefersDarkMode={props.prefersDarkMode} /> : ''
            }
          </Hidden>
          <div className={classes.grow} />
          <div className={classes.sectionDesktop}>
            <IconButton
              color="inherit"
              onClick={props.handleDrawerToggle}
              className={classes.menuButton}>
              <MenuIcon />
            </IconButton>

            <IconButton color="inherit"
              onClick={(e) => { handleChange(e) }}
            >
              <Brightness6Icon icon="sync-alt" />
            </IconButton>

            <IconButton color="inherit"
              onClick={(e) => { handleSubscriptions(e) }}
            >
              {
                subscribe ?
                  <FontAwesomeIcon icon="sync-alt" size='sm' spin />
                  :
                  <FontAwesomeIcon icon="sync-alt" size='sm' />
              }
            </IconButton>

            <IconButton color="inherit"
              onClick={(e) => { setOpen(!open) }}>
              <InfoIcon />
            </IconButton>

            <IconButton color="inherit"
              onClick={logout}>
              <ExitToAppIcon />
            </IconButton> 
          </div>


        </Toolbar>
      </AppBar>

      <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
        <DialogContent style={{ justifyContent: 'center', marginBottom: '0.5rem' }}>
          <Box display='flex' flexDirection='column' alignItems='center'>
            <Logo size='large'></Logo>

            <Typography variant="h4">SQD</Typography>

            <Typography >Sistema para la gestin y monitorizacin de squid</Typography>

            <Typography variant="h6">Versin 1.0.0</Typography>

            {
              error.show && <Alert variant="outlined" severity={error.type} style={{ marginBottom: '1rem', marginLeft: '1.6rem', marginRight: '1.6rem' }}>{error.msg}</Alert>
            }

            {
              props.license && <Alert variant="outlined" severity='success' style={{ marginTop: '1rem' }}> Licencia para {props.license.company} vlida por {props.license.timeleft === 1 ? '1 da restante' : props.license.timeleft + ' das restantes'}</Alert>
            }

            <small style={{ marginTop: '1.2rem' }}>Copyright &copy; Todos los derechos reservados </small>
          </Box>
        </DialogContent>
      </Dialog>
    </ThemeProvider>
  )
}