import React, { useState } from "react";
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import ConfirmDialog from "../dialog/confirm";
import * as Services from "../../services/services"
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Alert from '@material-ui/lab/Alert';
import CircularProgress from '@material-ui/core/CircularProgress';
import CloseIcon from '@material-ui/icons/Close';
import ThumbUpAltIcon from '@material-ui/icons/ThumbUpAlt';
import TextField from '@material-ui/core/TextField';
import { green } from '@material-ui/core/colors';
import VpnKeyIcon from '@material-ui/icons/VpnKey';

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
    margin: {
        margin: theme.spacing(0.3),
    },
    button: {
        margin: '0',
        minWidth: '0'
    },
    typography: {
        marginBottom: '0',
        marginLeft: '10px'
    },
    rounded: props => ({
        color: props.prefersDarkMode ? theme.palette.text.secondary : theme.palette.secondary,
        backgroundColor: props.prefersDarkMode ? '#343A40' : theme.palette.grey['400']
    }),
    listText: props => ({
        color: props.prefersDarkMode ? theme.palette.text.secondary : theme.palette.secondary
    }),
    wrapper: {
        margin: theme.spacing(1),
        position: 'relative',
    },
    buttonProgress: {
        color: green[500],
        position: 'absolute',
        top: '50%',
        left: '50%',
        marginTop: -12,
        marginLeft: -12,
    }
}));

var validator = require('validator');

export default function SSHItem(props) {
    const classes = useStyles(props);

    const [loading, setLoading] = useState(false);
    const [open, setOpen] = useState(false);
    const [server, setServer] = useState(null);

    const [newsshserver, setNewSSHServer] = useState({
        port: '22',
        username: 'root',
        password: ''
    })

    const [error, setError] = useState({
        show: false,
        type: 'error',
        msg: ''
    });

    const handleClose = () => {

        setOpen(false);

        setError({
            show: false,
            type: 'error',
            msg: ''
        })

        setLoading(false);

        setServer(null)

        setNewSSHServer({
            port: '22',
            username: 'root',
            password: ''
        })

    };

    const handleChange = (e) => {
        e.preventDefault()
        setNewSSHServer(newsshserver => ({ ...newsshserver, [e.target.name]: e.target.value }));
    }

    const handleSubmit = async (e) => {
        e.preventDefault()
        setError({
            show: false,
            type: 'error',
            msg: ''
        })

        if (newsshserver.username.length > 0 && validator.isEmpty(newsshserver.username, { ignore_whitespace: true })) {
            setError(error => ({ ...error, show: true, msg: 'El usuario no es vlido' }))
        } else
            if (!validator.isPort(newsshserver.port.toString())) {
                setError(error => ({ ...error, show: true, msg: 'El puerto no es vlido' }))
            } else
                if (newsshserver.username.length > 0 && validator.isEmpty(newsshserver.password, { ignore_whitespace: true })) {
                    setError(error => ({ ...error, show: true, msg: 'La contrasea no es vlida' }))
                } else {
                    if (!loading) {
                        setLoading(true);
                    }

                    await Services.setSSHConnection(props.server._id, newsshserver.username, newsshserver.port, newsshserver.password).then((response) => {
                        setLoading(false);
                        if (response.data.errors) {
                            console.log('error', response.data.errors)
                            setError(error => ({ ...error, show: true, msg: response.data.errors[0].msg }))
                        }
                        else {
                            props.showMessage('SSH configurado correctamente', 'success')
                            handleClose()
                        }
                    }).catch((err) => {
                        setLoading(false);
                        setError(error => ({ ...error, show: true, msg: 'Lo sentimos ha ocurrido un error inesperado.' }))
                    });
                }
    }

    const handleRemoveClick = async () => {
        if (!loading) {
            setLoading(true);
        }
        await Services.deleteSSHConnection(props.server._id).then((response) => {
            setLoading(false);
            if (response.data.errors)
                setError(error => ({ ...error, show: true, msg: response.data.errors[0].msg }))
            else {
                props.showMessage('Conexin SSH eliminada correctamente', 'success')
                handleClose()
            }
        }).catch((err) => {
            setLoading(false);
            setError(error => ({ ...error, show: true, msg: 'Lo sentimos ha ocurrido un error inesperado.' }))
        })
    }

    const loadServer = async () => {
        await Services.getServersById(props.server._id).then((response) => {
            if (!response.data.errors) {
                setServer(response.data)
            }
        }).catch((err) => {
            setError(error => ({ ...error, show: true, msg: 'Lo sentimos ha ocurrido un error inesperado.' }))
        })
    }

    return (<React.Fragment>
        <Button onClick={(e) => { e.preventDefault(); loadServer(); setOpen(true) }} variant="contained" color="secondary" size="small" className={classes.margin}
            startIcon={<VpnKeyIcon />}>
            SSH
        </Button>

        {
            server === null ?
                error.show &&
                <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
                    <DialogTitle id="form-dialog-title">ERROR</DialogTitle>
                    <Alert variant="outlined" severity={error.type} style={{ marginBottom: '1rem', marginLeft: '1.6rem', marginRight: '1.6rem' }}>{error.msg}</Alert>
                    <DialogActions style={{ justifyContent: 'center', marginBottom: '0.5rem' }}>
                        <div className={classes.wrapper}>
                            <Button
                                onClick={handleClose}
                                variant="contained"
                                color="secondary"
                                size="small"
                                className={classes.button}
                                startIcon={<ThumbUpAltIcon />}
                                disabled={loading}
                            >
                                Aceptar
                            </Button>
                            {loading && <CircularProgress size={24} className={classes.buttonProgress} />}
                        </div>
                    </DialogActions>
                </Dialog>
                :
                server.sshuser === undefined ?
                    <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
                        <form onSubmit={handleSubmit}>
                            <DialogTitle id="form-dialog-title">Configurar conexin SSH</DialogTitle>
                            <DialogContent>
                                {
                                    error.show && <Alert variant="outlined" severity={error.type} style={{ marginBottom: '1rem' }}>{error.msg}</Alert>
                                }

                                <Box display="flex" style={{ width: '100%' }}>
                                    <Box flexGrow={1} >
                                        <TextField
                                            onChange={handleChange}
                                            name="username"
                                            label="IP del servidor"
                                            variant="outlined"
                                            size="small"
                                            value={newsshserver.username}
                                        />
                                    </Box>
                                    <Box>
                                        <TextField
                                            onChange={handleChange}
                                            label="Puerto"
                                            name="port"
                                            type="number"
                                            inputProps={{ min: 1, max: 65536 }}
                                            InputLabelProps={{
                                                shrink: true,
                                            }}
                                            variant="outlined"
                                            size="small"
                                            value={newsshserver.port}
                                        />
                                    </Box>
                                </Box>
                                <Box display="flex" style={{ width: '100%', marginTop: '1rem' }}>

                                    <TextField style={{ width: '100%' }}
                                        onChange={handleChange}
                                        name="password"
                                        label="Contrasea"
                                        type="password"
                                        autoComplete="current-password"
                                        variant="outlined"
                                        size="small"
                                        value={newsshserver.password}
                                    />

                                </Box>
                            </DialogContent>
                            <DialogActions style={{ justifyContent: 'center', marginBottom: '0.5rem' }}>
                                <div className={classes.wrapper}>
                                    <Button
                                        type="submit"
                                        variant="contained"
                                        color="secondary"
                                        size="small"
                                        disabled={loading}
                                    >
                                        Aceptar
                                    </Button>
                                    {loading && <CircularProgress size={24} className={classes.buttonProgress} />}
                                </div>
                                <Button
                                    onClick={handleClose}
                                    variant="contained"
                                    color="default"
                                    sizeobando si existe conexiame={classes.button}
                                    startIcon={<CloseIcon />}
                                >
                                    Cancelar
                                </Button>
                            </DialogActions>
                        </form>
                    </Dialog>
                    : <ConfirmDialog open={open} loading={loading} handleClose={handleClose} handleConfirmClick={handleRemoveClick} >
                        <Typography variant="h6" gutterBottom> Ya existe una conexin SSH configurada desea eliminarla de forma permanente:</Typography>

                        <Alert variant="outlined" severity={error.type} style={{ marginBottom: '1rem' }}>Tenga en cuenta que podra dejar de funcionar las cuotas de usuario, los bloqueos automticos y por medio de la web, no podr reiniciar el servidor ni ver su estado.</Alert>

                    </ConfirmDialog>
        }


    </React.Fragment >);
}
