import React, { useState, useEffect } from "react";
import { createMuiTheme, ThemeProvider, makeStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';
import CssBaseline from '@material-ui/core/CssBaseline';
import Grid from '@material-ui/core/Grid';
import Hidden from '@material-ui/core/Hidden';
import socketIOClient from "socket.io-client";
import { useSnackbar } from 'notistack';
import Header from './components/header/header'
import Sidebar from './components/sidebar/sidebar'
import Content from './components/content/content'
import ServerItems from './components/serveritems/serveritems'
import ServerLdapItems from './components/serverldapitems/serverldapitems'
import TelegramChats from "./components/telegramchats/telegramchats";
import Resume from './components/resume/resume'
import ServerOptions from './components/serveroptions/serveroptions'
import LockedSources from "./components/lockedsources/lockedsources"
import Quotas from "./components/quotas/quotas"
import License from "./components/license/license"
import Login from "./components/login/login"
import * as Services from "./services/services"
import CloseIcon from '@material-ui/icons/Close';
import IconButton from '@material-ui/core/IconButton';
import { library } from '@fortawesome/fontawesome-svg-core'
import { fab } from '@fortawesome/free-brands-svg-icons'
import { faCheckSquare, faCoffee, faSyncAlt, faCheck, faArrowDown, faArrowUp } from '@fortawesome/free-solid-svg-icons'
import useToken from './hooks/useToken';

library.add(fab, faCheckSquare, faCoffee, faSyncAlt, faCheck, faArrowDown, faArrowUp)

const drawerWidth = 230;
const useStyles = makeStyles((theme) => ({
  root: {
    [theme.breakpoints.up('md')]: {
      flexGrow: 1,
    },
    [theme.breakpoints.up('lg')]: {
      display: 'flex',
    },
  },
  grow: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
    [theme.breakpoints.up('lg')]: {
      display: 'none',
    },
  },
  toolbar: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    padding: theme.spacing(2),
  },
  title: {
    display: 'none',
    [theme.breakpoints.up('sm')]: {
      display: 'block',
    },
  },
  small: {
    background: '#dc3545',
    borderRadius: '15px',
    color: 'white'
  }
}));

const socket = socketIOClient(process.env.REACT_APP_ENDPOINT);

export default function App(props) {
  const { enqueueSnackbar, closeSnackbar } = useSnackbar();

  //const queryPrefersDarkMode = useMediaQuery('(prefers-color-scheme: dark)');

  const [prefersDarkMode, setPrefersDarkMode] = useState(true);

  const theme = React.useMemo(
    () =>
      createMuiTheme({
        palette: {
          type: prefersDarkMode ? 'dark' : 'light',
          primary: {
            light: '#757ce8',
            main: '#dc3545', //'#3f50b5',
            dark: '#002884',
            contrastText: '#fff',
          },
          secondary: {
            light: '#ff7961', //focus
            main: '#dc3545', //background
            dark: '#ba000d', //hover
            contrastText: '#fff', // color
          },
          text: {
            secondary: '#9FA2A5'
          },
          background: {
            paper: prefersDarkMode ? '#343A40' : '#fff',
            default: prefersDarkMode ? '#212529' : '#e9ecef', //#fafafa
            grouprow: prefersDarkMode ? '#495057' : '#F5F5F5'
          }
        },
      }),
    [prefersDarkMode],
  );
  const { window } = props;
  const classes = useStyles();
  const [mobileOpen, setMobileOpen] = React.useState(false);
  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };
  const container = window !== undefined ? () => window().document.body : undefined;

  const [response, setResponse] = useState({});
  const [refreshSourcesLock, setRefreshSourcesLock] = useState(false);
  const [refreshQuota, setRefreshQuota] = useState(false);
  const [refreshChats, setRefreshChats] = useState(false);
  const [license, setLicense] = useState({ status: undefined, timeleft: 0, company: '' });

  const { token, setToken } = useToken();

  const showMessage = (msg, variant) => {
    const action = key => (
      <React.Fragment>
        <ThemeProvider theme={theme}>
          <IconButton onClick={() => { closeSnackbar(key) }} edge="end" aria-label="delete" style={{ marginRight: '3px' }}>
            <CloseIcon fontSize="small" />
          </IconButton>
        </ThemeProvider>
      </React.Fragment>
    );
    enqueueSnackbar(msg, {
      variant: variant,
      autoHideDuration: 3000,
      action
    })
  }

  const handleRefreshSourcesLock = async () => {
    setRefreshSourcesLock(!refreshSourcesLock)
  }

  const handleRefreshQuota = async () => {
    setRefreshQuota(!refreshQuota)
  }

  const handleRefreshChats = async () => {
    setRefreshChats(!refreshChats)
  }

  const handlePrefersDarkMode = async () => {
    await Services.updateConfig({ darktheme: !prefersDarkMode }).then((response) => {
      setPrefersDarkMode(response.data.darktheme)
    }).catch((error) => {
      return
    })
  }

  useEffect(() => {
    socket.on("proxyConnections", data => {
      console.log(data)
      setResponse(data)
    });

    socket.on("refreshSourcesLock", data => {
      setRefreshSourcesLock(refreshSourcesLock => ({ ...refreshSourcesLock, refreshSourcesLock: !refreshSourcesLock }))
    });

    socket.on("updateLicense", data => {
      setLicense(data.license)
    });

    async function getThemePrefers() {
      await Services.getThemePrefers().then((response) => {
        setPrefersDarkMode(response.data.darktheme)
      }).catch((error) => {
        return
      })
    }
    getThemePrefers();

    async function getConnectionsData() {
      await Services.getConnectionsData()
    }
    getConnectionsData();

  }, []);

  if (license.status !== undefined && !license.status) {
    return <ThemeProvider theme={theme}>
      <CssBaseline />
      <License showMessage={showMessage} />
    </ThemeProvider>
  } else if (!token) {
    return <ThemeProvider theme={theme}>
      <CssBaseline />
      <Login showMessage={showMessage} setToken={setToken} prefersDarkMode={prefersDarkMode} />
    </ThemeProvider>
  }

  return (
    <ThemeProvider theme={theme}>
      <div className={classes.root}>
        <CssBaseline />
        <Header
          handleRefreshSourcesLock={handleRefreshSourcesLock}
          showMessage={showMessage}
          handlePrefersDarkMode={handlePrefersDarkMode}
          prefersDarkMode={prefersDarkMode}
          handleDrawerToggle={handleDrawerToggle}
          drawerWidth={drawerWidth}
          socket={socket}
          server={response.server}
          serverIsUp={response.connections !== undefined && response.connections[0] !== undefined && response.connections[0].errors ? false : true}
          license={license}
          setToken={setToken}
        />
        <Sidebar showMessage={showMessage} handleRefreshChats={handleRefreshChats} mobileOpen={mobileOpen} prefersDarkMode={prefersDarkMode} handleDrawerToggle={handleDrawerToggle} container={container} connections={response.connections !== undefined && response.connections[0] !== undefined && !response.connections[0].errors ? response.connections : []} server={response.server !== undefined ? response.server : {}} />
        <main className={classes.content} >
          <div className={classes.toolbar} />
          {
            response.server !== undefined ?
              <Grid container spacing={3}>
                <Grid item xs={12}>
                  <Hidden mdUp implementation="css">
                    {
                      response.server !== undefined ? <ServerOptions handleRefreshSourcesLock={handleRefreshSourcesLock} showMessage={showMessage} server={response.server} serverIsUp={response.connections[0] !== undefined && response.connections[0].errors ? false : true} prefersDarkMode={prefersDarkMode} /> : ''
                    }
                  </Hidden>
                </Grid>
              </Grid>
              : ''
          }
          {<Resume
            usersConnected={response.usersConnected}
            usersConnections={response.usersConnections}
            usersSize={response.usersSize}
            usersSpeed={response.usersSpeed}
            prefersDarkMode={prefersDarkMode}
          />}
          <Grid container spacing={3}>
            <Grid item xs={12} md={9}>
              <Content showMessage={showMessage} handleRefreshQuota={handleRefreshQuota} handleRefreshSourcesLock={handleRefreshSourcesLock} usersConnected={response.usersConnected !== undefined ? response.usersConnected[response.usersConnected.length - 1] : 0} data={response.connections !== undefined && response.connections[0] !== undefined && !response.connections[0].errors ? response.connections : []} server={response.server !== undefined ? response.server : {}} />
            </Grid>
            <Grid item xs={12} md={3}>
              <Grid container spacing={2}>

                <ServerItems showMessage={showMessage} server={response.server !== undefined ? response.server : {}} />

                <ServerLdapItems showMessage={showMessage} server={response.server !== undefined ? response.server : {}} />

                <Quotas refreshQuota={refreshQuota} showMessage={showMessage} />

                {
                  response.server !== undefined ?
                    < LockedSources showMessage={showMessage} refreshSourcesLock={refreshSourcesLock} server={response.server} />
                    : ''
                }

                <TelegramChats showMessage={showMessage} refreshChats={refreshChats} />

              </Grid>
            </Grid>
          </Grid>
        </main>
      </div>
    </ThemeProvider>
  );
}

App.propTypes = {
  window: PropTypes.func,
};


