import React, { useState, useEffect } from "react";
import { createMuiTheme, ThemeProvider, makeStyles } from '@material-ui/core/styles';
import Avatar from '@material-ui/core/Avatar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import AssignmentIcon from '@material-ui/icons/Assignment';
import PersonIcon from '@material-ui/icons/Person';
import * as Services from "../../services/services"
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Alert from '@material-ui/lab/Alert';
import CircularProgress from '@material-ui/core/CircularProgress';
import ThumbUpAltIcon from '@material-ui/icons/ThumbUpAlt';
import TextField from '@material-ui/core/TextField';
import { green } from '@material-ui/core/colors';
import useMediaQuery from '@material-ui/core/useMediaQuery';

const useStyles = makeStyles((theme) => ({
    button: {
        margin: '0',
        minWidth: '0'
    },
    typography: {
        marginBottom: '0',
        marginLeft: '10px'
    },
    rounded: props => ({
        color: props.prefersDarkMode ? theme.palette.text.secondary : theme.palette.secondary,
        backgroundColor: props.prefersDarkMode ? '#343A40' : theme.palette.grey['400']
    }),
    listText: props => ({
        color: props.prefersDarkMode ? theme.palette.text.secondary : theme.palette.secondary
    }),
    small: {
        background: '#dc3545',
        borderRadius: '15px',
        color: 'white'
    },
    wrapper: {
        margin: theme.spacing(1),
        position: 'relative',
    },
    buttonProgress: {
        color: green[500],
        position: 'absolute',
        top: '50%',
        left: '50%',
        marginTop: -12,
        marginLeft: -12,
    }
}));

var validator = require('validator');

export default function ConnectionsWithoutUserItem(props) {
    const theme = React.useMemo(
        () =>
            createMuiTheme({
                palette: {
                    type: props.prefersDarkMode ? 'dark' : 'light',
                    primary: {
                        light: '#757ce8',
                        main: '#dc3545', //'#3f50b5',
                        dark: '#002884',
                        contrastText: '#fff',
                    },
                    secondary: {
                        light: '#ff7961', //focus
                        main: '#dc3545', //background
                        dark: '#ba000d', //hover
                        contrastText: '#fff', // color
                    },
                    text: {
                        secondary: '#9FA2A5'
                    },
                    background: {
                        paper: props.prefersDarkMode ? '#343A40' : '#fff',
                        default: props.prefersDarkMode ? '#212529' : '#fafafa',
                    }
                },
            }),
        [props.prefersDarkMode],
    );
    const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));

    const classes = useStyles(props);

    const [loading, setLoading] = useState(false);
    const [open, setOpen] = useState(false);

    const [conectionswithoutuser, setConectionsWithoutUser] = useState([])
    const [count, setCount] = useState(5)

    const [error, setError] = useState({
        show: false,
        type: 'error',
        msg: ''
    });

    const handleClose = () => {
        setOpen(false);

        setLoading(false);

        setConectionsWithoutUser([])

        setCount(5)

        setError({
            show: false,
            type: 'error',
            msg: ''
        })
    };

    const handleCountChange = (e) => {
        e.preventDefault()
        if (!validator.isInt(e.target.value.toString()) && e.target.value < 0) {
            setError(error => ({ ...error, show: true, msg: 'La cantidad no es vlida' }))
        } else {
            setCount(e.target.value)
            getConnectionsWithoutUser(e.target.value)
        }
    }

    const getConnectionsWithoutUser = (pcount) => {
        let tmpconectionswithoutuser = [];
        let sources = [];
        props.connections.map((connection, index) => {
            let source = connection.username === '-' ? connection.remote : connection.username
            if (Services.isIPAddress(source)) {
                if (sources.indexOf(source) === -1) {
                    sources.push(source)
                    tmpconectionswithoutuser.push({
                        identifier: source,
                        connections: 1,
                        activeconnections: connection.active ? 1 : 0
                    })
                } else {
                    tmpconectionswithoutuser[sources.indexOf(source)].connections++
                    if (connection.active) {
                        tmpconectionswithoutuser[sources.indexOf(source)].activeconnections++
                    }
                }
            }
            return connection
        });
        setConectionsWithoutUser(tmpconectionswithoutuser.slice(0, pcount));
    }

    useEffect(() => {
        const getConnectionsWithoutUser = (pcount) => {
            let tmpconectionswithoutuser = [];
            let sources = [];
            props.connections.map((connection, index) => {
                let source = connection.username === '-' ? connection.remote : connection.username
                if (Services.isIPAddress(source)) {
                    if (sources.indexOf(source) === -1) {
                        sources.push(source)
                        tmpconectionswithoutuser.push({
                            identifier: source,
                            connections: 1,
                            activeconnections: connection.active ? 1 : 0
                        })
                    } else {
                        tmpconectionswithoutuser[sources.indexOf(source)].connections++
                        if (connection.active) {
                            tmpconectionswithoutuser[sources.indexOf(source)].activeconnections++
                        }
                    }
                }
                return connection
            });
            setConectionsWithoutUser(tmpconectionswithoutuser.slice(0, pcount));
        }
        getConnectionsWithoutUser(count)
    }, [props.connections, count]);

    return (<React.Fragment>
        <ListItem button onClick={(e) => { e.preventDefault(e); getConnectionsWithoutUser(count); setOpen(true) }}>
            <ListItemAvatar>
                <Avatar variant="rounded" className={classes.rounded}>
                    <AssignmentIcon />
                </Avatar>
            </ListItemAvatar>
            <ListItemText primary='Navegacin mediante IP' className={classes.listText} />
        </ListItem>
        <ThemeProvider theme={theme}>
            {
                <Dialog fullScreen={fullScreen} fullWidth={true} maxWidth="sm" open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
                    <DialogTitle id="form-dialog-title">Navegacin mediante IP</DialogTitle>
                    <DialogContent>
                        {
                            error.show && <Alert variant="outlined" severity={error.type} style={{ marginBottom: '1rem' }}>{error.msg}</Alert>
                        }
                        <Box display="flex" style={{ width: '100%' }}>
                            <Box flexGrow={1} >
                                <TextField style={{ width: '100%' }}
                                    onChange={handleCountChange}
                                    label="Cantidad a mostrar"
                                    type="number"
                                    inputProps={{ min: 1 }}
                                    InputLabelProps={{
                                        shrink: true,
                                    }}
                                    variant="outlined"
                                    size="small"
                                    value={count}
                                />
                            </Box>
                        </Box>

                        {
                            conectionswithoutuser.length > 0 ?
                                conectionswithoutuser.map((conection, index) => {
                                    return (<Box display="flex" flexDirection='column'>
                                        <Box display="flex" alignItems="center" p={0.2} bgcolor="background.grouprow" style={{ borderRadius: '4px' }}>
                                            <Box display="flex" alignItems="center" flexGrow={1} >
                                                <Avatar>
                                                    <PersonIcon />
                                                </Avatar>
                                                <Typography variant="h6" style={{ marginLeft: '0.5rem' }}>
                                                    <b>{Services.parseSource(conection.identifier)}</b>
                                                </Typography>
                                            </Box>
                                            <small className={classes.small}><em style={{ margin: '5px' }}>{conection.connections}</em></small>
                                        </Box>
                                        <Box display="flex" flexDirection='column' style={{ textAlign: 'left', marginLeft: '3rem' }}>
                                            {
                                                conection.activeconnections === 0 ? 'Sin conexiones activas' : (conection.activeconnections === 1 ? conection.activeconnections + ' conexin activa' : conection.activeconnections + ' conexiones activas')
                                            }
                                        </Box>
                                    </Box>)
                                }) : <Typography>
                                    En estos momentos no existe ninguna ip navegando de forma directa
                                </Typography>
                        }

                    </DialogContent>
                    <DialogActions style={{ justifyContent: 'center', marginBottom: '0.5rem' }}>
                        <div className={classes.wrapper}>
                            <Button
                                onClick={(e) => { handleClose() }}
                                variant="contained"
                                color="secondary"
                                size="small"
                                className={classes.button}
                                startIcon={<ThumbUpAltIcon />}
                                disabled={loading}
                            >
                                Aceptar
                            </Button>
                            {loading && <CircularProgress size={24} className={classes.buttonProgress} />}
                        </div>
                    </DialogActions>
                </Dialog>
            }
        </ThemeProvider>
    </React.Fragment>);
}
