import React, { useState } from "react";
import { makeStyles } from '@material-ui/core/styles';
import Avatar from '@material-ui/core/Avatar';
import Typography from '@material-ui/core/Typography';
import DesktopWindowsIcon from '@material-ui/icons/DesktopWindows';
import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import ConfirmDialog from "../dialog/confirm";
import * as Services from "../../services/services"
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogTitle from '@material-ui/core/DialogTitle';
import Alert from '@material-ui/lab/Alert';
import CircularProgress from '@material-ui/core/CircularProgress';
import ThumbUpAltIcon from '@material-ui/icons/ThumbUpAlt';
import RefreshIcon from '@material-ui/icons/Refresh';

const useStyles = makeStyles((theme) => ({
    button: {
        margin: '0',
        minWidth: '0'
    },
    margin: {
        margin: theme.spacing(0.3),
    },
    typography: {
        marginBottom: '0',
        marginLeft: '10px'
    }
}));

export default function RestartItem(props) {
    const classes = useStyles();

    const [loading, setLoading] = useState(false);
    const [open, setOpen] = useState(false);
    const [server, setServer] = useState(null);
    const [error, setError] = useState({
        show: false,
        type: 'error',
        msg: ''
    });

    const handleRestartClick = async () => {
        if (!loading) {
            setLoading(true);
        }
        await Services.restartServer(props.server._id).then((response) => {
            setLoading(false);
            if (response.data.errors)
                props.showMessage(response.data.errors[0].msg, 'error')
            else {
                props.showMessage('Servidor reiniciado correctamente', 'success')
                handleClose()
            }
        }).catch((error) => {
            props.showMessage('Lo sentimos ha ocurrido un error inesperado.', 'error')
            handleClose()
        })
    }

    const handleClose = () => {
        setOpen(false);
        setLoading(false);
        setServer(null);
        setError({
            show: false,
            type: 'error',
            msg: ''
        })
    };

    const loadServer = async () => {
        await Services.getServersById(props.server._id).then((response) => {
            console.log(response.data)
            if (!response.data.errors) {
                setServer(response.data)
            }
        }).catch((err) => {
            setLoading(false);
            setError(error => ({ ...error, show: true, msg: 'Lo sentimos ha ocurrido un error inesperado.' }))
        })
    }

    return (<React.Fragment>

        <Button onClick={(e) => { e.preventDefault(); loadServer(); setOpen(true) }} variant="contained" color="secondary" size="small" className={classes.margin} disabled={props.server.sshuser === null ? true : false}
            startIcon={<RefreshIcon />}>
            Reiniciar
        </Button>

        {
            server == null ?

                error.show &&
                <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
                    <DialogTitle id="form-dialog-title">ERROR</DialogTitle>
                    <Alert variant="outlined" severity={error.type} style={{ marginBottom: '1rem', marginLeft: '1.6rem', marginRight: '1.6rem' }}>{error.msg}</Alert>
                    <DialogActions style={{ justifyContent: 'center', marginBottom: '0.5rem' }}>
                        <div className={classes.wrapper}>
                            <Button
                                onClick={handleClose}
                                variant="contained"
                                color="secondary"
                                size="small"
                                className={classes.button}
                                startIcon={<ThumbUpAltIcon />}
                                disabled={loading}
                            >
                                Aceptar
                                    </Button>
                            {loading && <CircularProgress size={24} className={classes.buttonProgress} />}
                        </div>
                    </DialogActions>
                </Dialog>
                :
                <ConfirmDialog open={open} loading={loading} handleClose={handleClose} handleConfirmClick={handleRestartClick} >
                    <Typography variant="h6" gutterBottom> Est a punto de reiniciar el servidor:</Typography>
                    <Box display="flex" alignItems="center" flexGrow={1} >
                        <Avatar>
                            <DesktopWindowsIcon fontSize="medium" />
                        </Avatar>
                        <Typography variant="h6" gutterBottom className={classes.typography}>
                            {props.server.host}
                        </Typography>
                    </Box>
                </ConfirmDialog>
        }


    </React.Fragment>);
}
