import React, { useState } from "react";
import { makeStyles } from '@material-ui/core/styles';
import Avatar from '@material-ui/core/Avatar';
import Typography from '@material-ui/core/Typography';
import DesktopWindowsIcon from '@material-ui/icons/DesktopWindows';
import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import DeleteOutlineIcon from '@material-ui/icons/DeleteOutline';
import ConfirmDialog from "../dialog/confirm";
import * as Services from "../../services/services"

const useStyles = makeStyles((theme) => ({
    button: {
        margin: '0',
        minWidth: '0'
    },
    typography: {
        marginBottom: '0',
        marginLeft: '10px'
    }
}));

export default function ServerLdapItem(props) {
    const classes = useStyles();

    const [loading, setLoading] = useState(false);
    const [open, setOpen] = useState(false);

    const handleRemoveClick = async () => {
        if (!loading) {
            setLoading(true);
        }
        await Services.deleteLdapServer(props.server._id).then((response) => {
            setLoading(false);
            if (response.data.errors)
                props.showMessage(response.data.errors[0].msg, 'error')
            else {
                props.showMessage('Servidor LDAP ' + response.data.host + ' eliminado correctamente', 'success')
                handleClose()
                props.loadLdapServers();
            }
        }).catch((error) => {
            setLoading(false);
            props.showMessage('Lo sentimos ha ocurrido un error inesperado.', 'error')
        })
    }

    const handleClose = () => {
        setOpen(false);
        setLoading(false);
    };

    return (
        <Box display="flex" alignItems="center" flexWrap="wrap" p={1} bgcolor="background.paper">
            <Box id={'server' + props.index} key={'server' + props.index} display="flex" alignItems="center" flexGrow={1} >
                <Avatar>
                    <DesktopWindowsIcon fontSize="medium" />
                </Avatar>
                <Typography color="textPrimary" variant="h6" gutterBottom className={classes.typography}>
                    {props.server.host}
                </Typography>
            </Box>
            <Box display="flex">
                <Button onClick={(e) => setOpen(true)} variant="contained" size="small" color="secondary" className={classes.button}>
                    <DeleteOutlineIcon />
                </Button>
            </Box>
            <ConfirmDialog open={open} loading={loading} handleClose={handleClose} handleConfirmClick={handleRemoveClick} >
                <Typography variant="h6" gutterBottom> Esta a punto de eliminar el servidor LDAP:</Typography>
                <Box display="flex" alignItems="center" flexGrow={1} >
                    <Avatar>
                        <DesktopWindowsIcon fontSize="medium" />
                    </Avatar>
                    <Typography variant="h6" gutterBottom className={classes.typography}>
                        {props.server.host}
                    </Typography>
                </Box>
            </ConfirmDialog>
        </Box>
    );
}
