import React, { useState } from "react";
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import TimelineIcon from '@material-ui/icons/Timeline';
import * as Services from "../../services/services"
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Alert from '@material-ui/lab/Alert';
import CircularProgress from '@material-ui/core/CircularProgress';
import CloseIcon from '@material-ui/icons/Close';
import ThumbUpAltIcon from '@material-ui/icons/ThumbUpAlt';
import TextField from '@material-ui/core/TextField';
import { green } from '@material-ui/core/colors';

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
    margin: {
        margin: theme.spacing(0.3),
    },
    button: {
        margin: '0',
        minWidth: '0'
    },
    typography: {
        marginBottom: '0',
        marginLeft: '10px'
    },
    rounded: props => ({
        color: props.prefersDarkMode ? theme.palette.text.secondary : theme.palette.secondary,
        backgroundColor: props.prefersDarkMode ? '#343A40' : theme.palette.grey['400']
    }),
    listText: props => ({
        color: props.prefersDarkMode ? theme.palette.text.secondary : theme.palette.secondary
    }),
    wrapper: {
        margin: theme.spacing(1),
        position: 'relative',
    },
    buttonProgress: {
        color: green[500],
        position: 'absolute',
        top: '50%',
        left: '50%',
        marginTop: -12,
        marginLeft: -12,
    }
}));

export default function RoutesItem(props) {
    const classes = useStyles(props);

    const [loading, setLoading] = useState(false);
    const [open, setOpen] = useState(false);

    const [server, setServer] = useState(null);
    const [routes, setRoutes] = useState({
        routelockuser: '',
        routelockpc: ''
    })

    const [error, setError] = useState({
        show: false,
        type: 'error',
        msg: ''
    });

    const handleClose = () => {

        setOpen(false);

        setError({
            show: false,
            type: 'error',
            msg: ''
        })

        setLoading(false);

        setServer(null)

        setRoutes({
            routelockuser: '',
            routelockpc: ''
        })
    };

    const handleChange = (e) => {
        e.preventDefault()
        setRoutes(routes => ({ ...routes, [e.target.name]: e.target.value }));
    }

    const handleSubmit = async (e) => {
        e.preventDefault()
        setError({
            show: false,
            type: 'error',
            msg: ''
        })

        if (!loading) {
            setLoading(true);
        }

        await Services.updateServer(props.server._id, { routelockuser: routes.routelockuser, routelockpc: routes.routelockpc }).then((response) => {
            setLoading(false);

            if (response.data.errors)
                setError(error => ({ ...error, show: true, msg: response.data.errors[0].msg }))
            else {
                props.showMessage('Rutas de bloqueo registradas correctamente!', 'success')
                handleClose()
            }
        }).catch((err) => {
            setLoading(false);
            setError(error => ({ ...error, show: true, msg: 'Lo sentimos ha ocurrido un error inesperado.' }))
        });
    }

    const loadServer = async () => {
        await Services.getServersById(props.server._id).then((response) => {
            if (!response.data.errors) {
                setServer(response.data)
                setRoutes({
                    routelockuser: response.data.routelockuser ? response.data.routelockuser : '',
                    routelockpc: response.data.routelockpc ? response.data.routelockpc : ''
                })
            }
        }).catch((err) => {
            setLoading(false);
            setError(error => ({ ...error, show: true, msg: 'Lo sentimos ha ocurrido un error inesperado.' }))
        })
    }

    return (<React.Fragment>
        <Button onClick={(e) => { e.preventDefault(); loadServer(); setOpen(true) }} variant="contained" color="secondary" size="small" className={classes.margin}
            startIcon={<TimelineIcon />}>
            Rutas
        </Button>

        {
            server == null ?
                error.show &&
                <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
                    <DialogTitle id="form-dialog-title">ERROR</DialogTitle>
                    <Alert variant="outlined" severity={error.type} style={{ marginBottom: '1rem', marginLeft: '1.6rem', marginRight: '1.6rem' }}>{error.msg}</Alert>
                    <DialogActions style={{ justifyContent: 'center', marginBottom: '0.5rem' }}>
                        <div className={classes.wrapper}>
                            <Button
                                onClick={handleClose}
                                variant="contained"
                                color="secondary"
                                size="small"
                                className={classes.button}
                                startIcon={<ThumbUpAltIcon />}
                                disabled={loading}
                            >
                                Aceptar
                            </Button>
                            {loading && <CircularProgress size={24} className={classes.buttonProgress} />}
                        </div>
                    </DialogActions>
                </Dialog>
                :
                <Dialog fullWidth={true} maxWidth="sm" open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
                    <form onSubmit={handleSubmit}>
                        <DialogTitle id="form-dialog-title">Rutas de bloqueo</DialogTitle>
                        <DialogContent style={{ overflowY: 'inherit' }}>

                            {
                                error.show && <Alert variant="outlined" severity={error.type} style={{ marginBottom: '1rem' }}>{error.msg}</Alert>
                            }

                            <Box style={{ width: '100%' }}>
                                <TextField style={{ width: '100%' }}
                                    onChange={handleChange}
                                    name="routelockuser"
                                    label="Ruta de bloqueo para usuarios"
                                    variant="outlined"
                                    size="small"
                                    defaultValue={server.routelockuser ? server.routelockuser : ''}
                                />
                            </Box>
                            <Box style={{ width: '100%' }}>
                                <TextField style={{ width: '100%', marginTop: '1rem' }}
                                    onChange={handleChange}
                                    name="routelockpc"
                                    label="Ruta de bloqueo para pc"
                                    variant="outlined"
                                    size="small"
                                    defaultValue={server.routelockpc ? server.routelockpc : ''}
                                />
                            </Box>

                        </DialogContent>
                        <DialogActions style={{ justifyContent: 'center', marginBottom: '0.5rem' }}>
                            <div className={classes.wrapper}>
                                <Button
                                    type="submit"
                                    variant="contained"
                                    color="secondary"
                                    size="small"
                                    className={classes.button}
                                    startIcon={<ThumbUpAltIcon />}
                                    disabled={loading}
                                >
                                    Aceptar
                                </Button>
                                {loading && <CircularProgress size={24} className={classes.buttonProgress} />}
                            </div>
                            <Button
                                onClick={handleClose}
                                variant="contained"
                                color="default"
                                size="small"
                                className={classes.button}
                                startIcon={<CloseIcon />}
                            >
                                Cancelar
                            </Button>
                        </DialogActions>
                    </form>
                </Dialog>
        }


    </React.Fragment >);
}
