import React from 'react'
import ResumeItem from './resumeitem';
import Grid from '@material-ui/core/Grid';
import LinkIcon from '@material-ui/icons/Link';
import PeopleIcon from '@material-ui/icons/People';
import ImportExportIcon from '@material-ui/icons/ImportExport';
import GetAppIcon from '@material-ui/icons/GetApp';

export default function Resume(props) {
    return (
        <Grid container spacing={3}>
            <Grid item xs={12} sm={6} md={3}>
                <ResumeItem prefersDarkMode={props.prefersDarkMode} data={props.usersConnected !== undefined ? props.usersConnected : []} type='connected' graphicid="chart-connected" value='23' text='Conectados' icon={<PeopleIcon style={{ fontSize: 40 }} />} />
            </Grid>
            <Grid item xs={12} sm={6} md={3}>
                <ResumeItem prefersDarkMode={props.prefersDarkMode} data={props.usersConnections !== undefined ? props.usersConnections : []} type='connections' graphicid="chart-connections" value='50' text='Conexiones' icon={<LinkIcon style={{ fontSize: 40 }} />} />
            </Grid>
            <Grid item xs={12} sm={6} md={3}>
                <ResumeItem prefersDarkMode={props.prefersDarkMode} data={props.usersSize !== undefined ? props.usersSize : []} type='size' graphicid="chart-size" value='302MB' text='Tamao' icon={<GetAppIcon style={{ fontSize: 40 }} />} />
            </Grid>
            <Grid item xs={12} sm={6} md={3}>
                <ResumeItem prefersDarkMode={props.prefersDarkMode} data={props.usersSpeed !== undefined ? props.usersSpeed : []} type='speed' graphicid="chart-speed" value='475KB/s' text='Velocidad' icon={<ImportExportIcon style={{ fontSize: 40 }} />} subvalue='-55%' />
            </Grid>
        </Grid>
    );
}
