import React, { useState } from "react";
import { makeStyles } from '@material-ui/core/styles';
import Avatar from '@material-ui/core/Avatar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import Badge from '@material-ui/core/Badge';
import GroupIcon from '@material-ui/icons/Group';
import DeleteOutlineIcon from '@material-ui/icons/DeleteOutline';
import SpeedOutlinedIcon from '@material-ui/icons/SpeedOutlined';
import CheckCircleIcon from '@material-ui/icons/CheckCircle';

import * as Services from "../../services/services"
import ConfirmDialog from "../dialog/confirm";

const useStyles = makeStyles((theme) => ({
    button: {
        margin: '0',
        minWidth: '0'
    },
    typography: {
        marginBottom: '0',
        marginLeft: '10px',
        display: 'flex'
    }
}));

export default function Quota(props) {
    const classes = useStyles();

    const [loading, setLoading] = useState(false);
    const [open, setOpen] = useState(false);

    const handleRemoveClick = async () => {
        if (!loading) {
            setLoading(true);
        }
        await Services.deleteQuota(props.quota._id).then((response) => {
            setLoading(false);
            if (response.data.errors)
                props.showMessage(response.data.errors[0].msg, 'error')
            else {
                props.showMessage('Cuota eliminada correctamente', 'success')
                handleClose()
                props.loadQuotas();
            }
        }).catch((error) => {
            setLoading(false);
            props.showMessage('Lo sentimos ha ocurrido un error inesperado.', 'error')
        })
    }

    const handleClose = () => {
        setOpen(false);
        setLoading(false);
    };

    return (
        <Box display="flex" alignItems="center" flexWrap="wrap" p={1} bgcolor="background.paper">
            <Box id={'server' + props.index} key={'server' + props.index} display="flex" alignItems="center" flexGrow={1} >
                <Avatar>
                    <SpeedOutlinedIcon fontSize="medium" />
                </Avatar>
                <Box display="flex" flexGrow={10} flexDirection='column'>
                    <Typography color="textPrimary" gutterBottom className={classes.typography}>
                        {Services.getSize(props.quota.limit)} {props.quota.isdefault ? <CheckCircleIcon /> : ''}
                    </Typography>

                    <Typography gutterBottom className={classes.typography}>
                    <em>{Services.convertQuotaType(props.quota.type)}</em>
                    </Typography>
                </Box>
            </Box>
            <Box display="flex" >
                <Badge badgeContent={props.quota.sources.length} max={9} color="primary">
                    <GroupIcon fontSize="large" />
                </Badge>
                <Button onClick={(e) => setOpen(true)} variant="contained" size="small" color="secondary" className={classes.button} style={{ marginLeft: '25px' }}>
                    <DeleteOutlineIcon />
                </Button>
            </Box>
            <ConfirmDialog open={open} loading={loading} handleClose={handleClose} handleConfirmClick={handleRemoveClick} >
                <Typography variant="h6" gutterBottom> Est a punto de eliminar esta cuota:</Typography>
                <Box display="flex" alignItems="center" flexGrow={1} >
                    <Avatar>
                        <SpeedOutlinedIcon fontSize="medium" />
                    </Avatar>
                    <Box display="flex" flexGrow={10} flexDirection='column'>
                        <Typography gutterBottom className={classes.typography}>
                            {Services.getSize(props.quota.limit)} {props.quota.isdefault ? <CheckCircleIcon /> : ''}
                        </Typography>

                        <Typography gutterBottom className={classes.typography}>
                          <em> {Services.convertQuotaType(props.quota.type)}</em>
                        </Typography>
                    </Box>
                </Box>
            </ConfirmDialog>
        </Box>
    );
}
