import React, { useState } from "react";
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import * as Services from "../../services/services"
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Alert from '@material-ui/lab/Alert';
import CircularProgress from '@material-ui/core/CircularProgress';
import ThumbUpAltIcon from '@material-ui/icons/ThumbUpAlt';
import AssignmentTurnedInIcon from '@material-ui/icons/AssignmentTurnedIn';
import { green } from '@material-ui/core/colors';

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
    margin: {
        margin: theme.spacing(0.3),
    },
    button: {
        margin: '0',
        minWidth: '0'
    },
    typography: {
        marginBottom: '0',
        marginLeft: '10px'
    },
    rounded: props => ({
        color: props.prefersDarkMode ? theme.palette.text.secondary : theme.palette.secondary,
        backgroundColor: props.prefersDarkMode ? '#343A40' : theme.palette.grey['400']
    }),
    listText: props => ({
        color: props.prefersDarkMode ? theme.palette.text.secondary : theme.palette.secondary
    }),
    wrapper: {
        margin: theme.spacing(1),
        position: 'relative',
    },
    buttonProgress: {
        color: green[500],
        position: 'absolute',
        top: '50%',
        left: '50%',
        marginTop: -12,
        marginLeft: -12,
    }
}));

export default function StatusItem(props) {
    const classes = useStyles(props);

    const [loading, setLoading] = useState(false);
    const [open, setOpen] = useState(false);
    const [status, setStatus] = useState(null);

    const [error, setError] = useState({
        show: false,
        type: 'error',
        msg: ''
    });

    const handleClose = () => {
        setOpen(false);
        setError({
            show: false,
            type: 'error',
            msg: ''
        })
        setLoading(false);
        setStatus(null)
    };

    const loadStatus = async () => {
        await Services.statusServer(props.server._id).then((response) => {
            console.log(response.data)
            if (response.data.errors) {
                setError(error => ({ ...error, show: true, msg: response.data.errors[0].msg }))
            } else {
                setStatus(response.data)
            }
        }).catch((err) => {
            setLoading(false);
            setError(error => ({ ...error, show: true, msg: 'Lo sentimos ha ocurrido un error inesperado.' }))
        })
    }

    return (<React.Fragment>
        <Button onClick={(e) => { e.preventDefault(); loadStatus(); setOpen(true) }} variant="contained" color="secondary" size="small" className={classes.margin} disabled={props.server.sshuser === null ? true : false}
            startIcon={<AssignmentTurnedInIcon />}>
            Estado
        </Button>

        {
            status === null ?
                <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
                    {
                        error.show ?
                            <React.Fragment>
                                <DialogTitle id="form-dialog-title">ERROR</DialogTitle>
                                <Alert variant="outlined" severity={error.type} style={{ marginBottom: '1rem', marginLeft: '1.6rem', marginRight: '1.6rem' }}>{error.msg}</Alert>
                                <DialogActions style={{ justifyContent: 'center', marginBottom: '0.5rem' }}>
                                    <div className={classes.wrapper}>
                                        <Button
                                            onClick={handleClose}
                                            variant="contained"
                                            color="secondary"
                                            size="small"
                                            className={classes.button}
                                            startIcon={<ThumbUpAltIcon />}
                                            disabled={loading}
                                        >
                                            Aceptar
                                        </Button>
                                        {loading && <CircularProgress size={24} className={classes.buttonProgress} />}
                                    </div>
                                </DialogActions>
                            </React.Fragment>
                            :
                            <DialogContent>
                                <div className={classes.root}>
                                    <CircularProgress color="secondary" />
                                    <Typography variant="h6">Comprobando estado del servidor</Typography>
                                </div>
                            </DialogContent>

                    }
                </Dialog>
                :

                <Dialog fullWidth={true} maxWidth="lg" open={open} onClose={handleClose} aria-labelledby="form-dialog-title">

                    <DialogTitle id="form-dialog-title">Detalles del estado del servidor</DialogTitle>
                    <DialogContent>
                        {
                            error.show && <Alert variant="outlined" severity={error.type} style={{ marginBottom: '1rem' }}>{error.msg}</Alert>
                        }

                        <Box component="span">
                            <Box overflow="auto" style={{ textAlign: 'left' }}>
                                <pre style={{ maxWidth: '20rem' }}>{status.detail}</pre>
                            </Box>
                        </Box>

                    </DialogContent>
                    <DialogActions style={{ justifyContent: 'center', marginBottom: '0.5rem' }}>
                        <div className={classes.wrapper}>
                            <Button
                                onClick={handleClose}
                                variant="contained"
                                color="secondary"
                                size="small"
                                className={classes.button}
                                startIcon={<ThumbUpAltIcon />}
                                disabled={loading}
                            >
                                Aceptar
                            </Button>
                            {loading && <CircularProgress size={24} className={classes.buttonProgress} />}
                        </div>
                    </DialogActions>
                </Dialog>
        }


    </React.Fragment>);
}
