import React, { useState, useEffect } from "react";
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import Grid from '@material-ui/core/Grid';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import ThumbUpAltIcon from '@material-ui/icons/ThumbUpAlt';
import ControlPointIcon from '@material-ui/icons/ControlPoint';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import InputLabel from '@material-ui/core/InputLabel';
import TextField from '@material-ui/core/TextField';
import SpeedOutlinedIcon from '@material-ui/icons/SpeedOutlined';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Alert from '@material-ui/lab/Alert';
import CircularProgress from '@material-ui/core/CircularProgress';
import { green } from '@material-ui/core/colors';
import * as Services from "../../services/services"
import Quota from "./quota"

const useStyles = makeStyles((theme) => ({
    paper: {
        padding: theme.spacing(2),
        color: theme.palette.text.secondary,
        borderRadius: '1rem'
    },
    button: {
        margin: '0',
        minWidth: '0'
    },
    typography: {
        marginBottom: '0',
        marginLeft: '10px',
        display: 'flex'
    },
    wrapper: {
        margin: theme.spacing(1),
        position: 'relative',
    },
    buttonProgress: {
        color: green[500],
        position: 'absolute',
        top: '50%',
        left: '50%',
        marginTop: -12,
        marginLeft: -12,
    }
}));

export default function Quotas(props) {
    const classes = useStyles();
    const [quotas, setQuotas] = useState(null)

    const [loading, setLoading] = useState(false);

    const [newquota, setNewQuota] = useState({
        limit: '1',
        limittype: 'GB',
        type: 'monthly'
    })
    const [defaulquota, setDefaultQuota] = useState('nodefault')

    const [error, setError] = useState({
        show: false,
        type: 'error',
        msg: ''
    });

    const [open, setOpen] = useState(false);

    const [openquotadialog, setOpenQuotaDialog] = useState(false);

    const [quotasHidden, setQuotasHidden] = useState(true)

    const handleClose = () => {
        setOpen(false);

        setNewQuota({
            limit: '1',
            limittype: 'GB',
            type: 'monthly'
        })

        setError({
            show: false,
            type: 'error',
            msg: ''
        })
    };
    const handleCloseQuotaDialog = () => {
        setOpenQuotaDialog(false);

        setDefaultQuota('nodefault')

        setError({
            show: false,
            type: 'error',
            msg: ''
        })
    };

    const handleChange = (e) => {
        e.preventDefault()
        setNewQuota(newquota => ({ ...newquota, [e.target.name]: e.target.value }));
    }
    const handleChangeQuotaDialog = (e) => {
        e.preventDefault()
        setDefaultQuota(e.target.value);
    }

    const handleSubmitQuotaDialog = async () => {
        setError({
            show: false,
            type: 'error',
            msg: ''
        })
        if (!loading) {
            setLoading(true);
        }
        await Services.setDefaultQuota({ quotaid: defaulquota })
            .then((response) => {
                setLoading(false);
                if (response.data.errors)
                    setError(error => ({ ...error, show: true, msg: response.data.errors[0].msg }))
                else {
                    if (response.data.status !== "nothingtodo") {
                        props.showMessage(response.data.msg, 'success')
                        handleCloseQuotaDialog()
                        loadQuotas()
                    } else
                        setError(error => ({ ...error, show: true, msg: 'Seleccione una cuota de la lista' }))
                }
            }).catch((error) => {
                setLoading(false);
                setError(error => ({ ...error, show: true, msg: 'Lo sentimos ha ocurrido un error inesperado.' }))
            });
    }

    const handleSubmit = async (e) => {
        e.preventDefault()
        setError({
            show: false,
            type: 'error',
            msg: ''
        })
        if (Number(newquota.limit) < 1) {
            setError(error => ({ ...error, show: true, msg: 'El lmite no es vlido' }))
        } else {
            if (!loading) {
                setLoading(true);
            }
            await Services.registerQuota({ limit: Services.getBytes(Number(newquota.limit), newquota.limittype), type: newquota.type })
                .then((response) => {
                    setLoading(false);
                    if (response.data.errors)
                        setError(error => ({ ...error, show: true, msg: response.data.errors[0].msg }))
                    else {
                        props.showMessage('Cuota registrada correctamente!', 'success')
                        handleClose()
                        loadQuotas()
                    }
                }).catch((error) => {
                    setLoading(false);
                    setError(error => ({ ...error, show: true, msg: 'Lo sentimos ha ocurrido un error inesperado.' }))
                });
        }
    }

    const loadQuotas = async () => {
        await Services.getQuotas().then((response) => {
            console.log(response.data);
            if (!response.data.errors) {
                setQuotas(response.data)
            }
        }).catch((error) => {
            return
        })
    }

    useEffect(() => {
        loadQuotas()
    }, [props.refreshQuota]);

    return (

        <React.Fragment>
            {
                quotas !== null ?
                    <Grid item xs={12} sm={12} >
                        <Paper className={classes.paper}>
                            <Box display="flex" alignItems="center" flexGrow={1}>
                                <Box flexGrow={1}>
                                    <Typography color="textPrimary" variant="h6">Cuotas</Typography>
                                </Box>
                                <IconButton onClick={(e) => { e.preventDefault(); setOpen(true) }} edge="end" aria-label="add">
                                    <ControlPointIcon />
                                </IconButton>
                                <IconButton onClick={(e) => { e.preventDefault(); setOpenQuotaDialog(true) }} edge="end" aria-label="add">
                                    <SpeedOutlinedIcon />
                                </IconButton>
                            </Box>
                            <div style={{ width: '100%' }}>
                                {
                                    quotas.length > 0 &&
                                    quotas.slice(0, 3).map((quota, index) => {
                                        return <Quota key={'quota' + index} loadQuotas={loadQuotas} showMessage={props.showMessage} quota={quota} />
                                    })
                                }
                            </div>

                            {
                                quotas.length > 3 &&
                                <React.Fragment>
                                    <div style={{ width: '100%', display: quotasHidden ? 'none' : 'inherit' }}>
                                        {
                                            quotas.slice(3, quotas.length).map((quota, index) => {
                                                return <Quota key={'quota' + index} loadQuotas={loadQuotas} showMessage={props.showMessage} quota={quota} />
                                            })
                                        }
                                    </div>
                                    <Box display="flex" alignItems="center" justifyContent="center">
                                        <Button onClick={() => { setQuotasHidden(!quotasHidden) }}>{quotasHidden ? 'Ver todos' : 'Volver a ocultar'}</Button>
                                    </Box>
                                </React.Fragment>
                            }
                        </Paper>
                        <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
                            <form onSubmit={handleSubmit}>
                                <DialogTitle id="form-dialog-title">Registrar Quota</DialogTitle>
                                <DialogContent>
                                    {
                                        error.show && <Alert variant="outlined" severity={error.type} style={{ marginBottom: '1rem' }}>{error.msg}</Alert>
                                    }

                                    <Box display="flex" style={{ width: '100%' }}>
                                        <Box flexGrow={1} >
                                            <TextField style={{ width: '100%' }}
                                                onChange={handleChange}
                                                id="quota-limit"
                                                name="limit"
                                                label="Limite"
                                                variant="outlined"
                                                size="small"
                                                type="number"
                                                defaultValue={newquota.limit}
                                                value={newquota.limit}
                                                inputProps={{ min: 1 }}
                                                InputLabelProps={{ shrink: true }}
                                            />
                                        </Box>
                                        <Box>
                                            <FormControl size="small" variant="outlined">
                                                <InputLabel htmlFor="quota-limit-type">Medida</InputLabel>
                                                <Select
                                                    onChange={handleChange}
                                                    native
                                                    label="Medida"
                                                    defaultValue={newquota.limittype}
                                                    inputProps={{
                                                        name: 'limittype',
                                                        id: 'quota-limit-type',
                                                        value: newquota.limittype
                                                    }}
                                                >
                                                    <option value={'TB'}>TB</option>
                                                    <option value={'GB'}>GB</option>
                                                    <option value={'MB'}>MB</option>
                                                    <option value={'KB'}>KB</option>
                                                </Select>
                                            </FormControl>
                                        </Box>
                                    </Box>
                                    <Box style={{ width: '100%', marginTop: '1rem' }}>
                                        <FormControl size="small" variant="outlined" style={{ width: '100%' }}>
                                            <InputLabel htmlFor="quota-type">Tipo de Cuota</InputLabel>
                                            <Select style={{ width: '100%' }}
                                                onChange={handleChange}
                                                native
                                                label="Tipo de Cuota"
                                                defaultValue={newquota.type}
                                                value={newquota.type}
                                                inputProps={{
                                                    name: 'type',
                                                    id: 'quota-type',
                                                    value: newquota.type
                                                }}
                                            >
                                                <option value={'monthly'}>Mensual</option>
                                                <option value={'weekly'}>Semanal</option>
                                                <option value={'daily'}>Diario</option>
                                                <option value={'hourly'}>Cada hora</option>
                                            </Select>
                                        </FormControl>
                                    </Box>
                                </DialogContent>
                                <DialogActions style={{ justifyContent: 'center', marginBottom: '0.5rem' }}>
                                    <div className={classes.wrapper}>
                                        <Button
                                            type="submit"
                                            variant="contained"
                                            color="secondary"
                                            size="small"
                                            className={classes.button}
                                            startIcon={<ThumbUpAltIcon />}
                                            disabled={loading}
                                        >
                                            Aceptar
                                        </Button>
                                        {loading && <CircularProgress size={24} className={classes.buttonProgress} />}
                                    </div>
                                    <Button
                                        onClick={handleClose}
                                        variant="contained"
                                        color="default"
                                        size="small"
                                        className={classes.button}
                                        startIcon={<CloseIcon />}
                                    >
                                        Cancelar
                                    </Button>
                                </DialogActions>
                            </form>
                        </Dialog>
                        <Dialog open={openquotadialog} onClose={handleCloseQuotaDialog} aria-labelledby="form-dialog-title">
                            <DialogTitle id="form-dialog-title">Establecer cuota por defecto</DialogTitle>
                            <DialogContent>
                                {
                                    error.show && <Alert variant="outlined" severity={error.type} style={{ marginBottom: '1rem' }}>{error.msg}</Alert>
                                }

                                {
                                    quotas.map((value, index) => {
                                        if (value.isdefault) {
                                            return <Box style={{ width: '100%', textAlign: 'left', marginBottom: '1rem' }}><small>
                                                <em>Actualmente ya tiene la cuota <b>{Services.getSize(value.limit) + ' - ' + Services.convertQuotaType(value.type)}</b> establecida como cuota por defecto. Seleccione la opcin "Sin cuota por defecto" si lo que desea es eliminarla.</em>
                                            </small>
                                            </Box>
                                        }
                                        return ''
                                    })
                                }

                                <Box style={{ width: '100%' }}>
                                    <FormControl size="small" variant="outlined" style={{ width: '100%' }}>
                                        <InputLabel htmlFor="set-default-quota">Tipo de Cuota</InputLabel>
                                        <Select style={{ width: '100%' }}
                                            onChange={handleChangeQuotaDialog}
                                            native
                                            label="Tipo de Cuota"
                                            inputProps={{
                                                name: 'limittype',
                                                id: 'set-default-quota',
                                                vale: defaulquota
                                            }}
                                        >
                                            <option value="nodefault">Sin cuota por defecto</option>
                                            {
                                                quotas !== null && !quotas.errors ?
                                                    quotas.map((quota, index) => {
                                                        return <option value={quota._id}>{Services.getSize(quota.limit) + ' - ' + Services.convertQuotaType(quota.type)}</option>
                                                    })
                                                    : ''
                                            }
                                        </Select>
                                    </FormControl>
                                </Box>

                            </DialogContent>
                            <DialogActions style={{ justifyContent: 'center', marginBottom: '0.5rem' }}>
                                <div className={classes.wrapper}>
                                    <Button
                                        onClick={handleSubmitQuotaDialog}
                                        variant="contained"
                                        color="secondary"
                                        size="small"
                                        className={classes.button}
                                        startIcon={<ThumbUpAltIcon />}
                                        disabled={loading}
                                    >
                                        Aceptar
                                    </Button>
                                    {loading && <CircularProgress size={24} className={classes.buttonProgress} />}
                                </div>
                                <Button
                                    onClick={handleCloseQuotaDialog}
                                    variant="contained"
                                    color="default"
                                    size="small"
                                    className={classes.button}
                                    startIcon={<CloseIcon />}
                                >
                                    Cancelar
                                </Button>
                            </DialogActions>
                        </Dialog>
                    </Grid>
                    : ''

            }

        </React.Fragment>
    );
}
