export const convertQuotaType = (type) => {
  let converted = ''
  switch (type) {
    case 'weekly':
      converted = 'Semanal'
      break;
    case 'daily':
      converted = 'Diario'
      break;
    case 'hourly':
      converted = 'Cada hora'
      break;
    default:
      converted = 'Mensual'
      break;
  }
  return converted
}

export const getSize = (size) => {
  let mod = 1024;
  let pos = 0
  let units = ["B", "KB", "MB", "GB", "TB", "PB", "EB", "ZB", "YB"];
  for (let i = 0; size > mod; i++) {
    size /= mod;
    pos++
  }
  return +size.toFixed(2) + units[pos];
}

export const getBytes = (size, unit) => {
  if (unit === "YB")
    return size * Math.pow(1024, 8)
  if (unit === "ZB")
    return size * Math.pow(1024, 7)
  if (unit === "EB")
    return size * Math.pow(1024, 6)
  if (unit === "PB")
    return size * Math.pow(1024, 5)
  if (unit === "TB")
    return size * Math.pow(1024, 4)
  if (unit === "GB")
    return size * Math.pow(1024, 3)
  if (unit === "MB")
    return size * Math.pow(1024, 2)
  if (unit === "KB")
    return size * 1024
  else return size
}

export const getTime = (time, lang) => {
  if (time === 0)
    return lang === 'es' ? 'ahora' : 'now'
  let timeobj = hdate.relativeTime(time * -1, { returnObject: true })
  let timelang = ''
  if (timeobj.years > 0)
    timelang += lang === 'es' ? timeobj.years + 'a' : timeobj.years + 'y'
  if (timeobj.days > 0)
    timelang += ' ' + timeobj.days + 'd'
  if (timeobj.hours > 0)
    timelang += ' ' + timeobj.hours + 'h'
  if (timeobj.minutes > 0)
    timelang += ' ' + timeobj.minutes + 'm'
  if (timeobj.seconds > 0)
    timelang += ' ' + timeobj.seconds + 's'
  return timelang.trim()
}

