export const parseSource = (source) => {
  return source.split('@')[0]
}

export const isIPAddress = (value) => {
  return /^(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)$/.test(value)
}

export const extractProxyData = (uri) => {
  let data = {
    host: '',
    port: '',
    username: '',
    password: ''
  }
  let urisplit = uri.split('//')[1].split('@')
  if (urisplit[1]) {
    data.host = urisplit[1].split(':')[0]
    data.port = urisplit[1].split(':')[1]
    data.username = urisplit[0].split(':')[0]
    data.password = urisplit[0].split(':')[1]
  } else {
    data.host = urisplit[0].split(':')[0]
    data.port = urisplit[0].split(':')[1]
  }
  return data
}

export const getClearUri = (uri) => {
  let spliturl = uri.split(':');
  let urlready = null;
  if (spliturl[0] === 'http' || spliturl[0] === 'https') {
    let tmpurl = spliturl[1].split('/');
    urlready = tmpurl[2];
  } else
    urlready = spliturl[0];

  return urlready;
}


