import React, { useState, useEffect } from "react";
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Box from '@material-ui/core/Box';
import Grid from '@material-ui/core/Grid';
import Badge from '@material-ui/core/Badge';
import Button from '@material-ui/core/Button';
import 'moment/locale/es';
import * as Services from "../../services/services"
import LockedSource from "./lockedsource"

const useStyles = makeStyles((theme) => ({
    paper: {
        padding: theme.spacing(2),
        color: theme.palette.text.secondary,
        borderRadius: '1rem'
    },
    button: {
        margin: '0',
        minWidth: '0'
    },
    typography: {
        marginBottom: '0',
        marginLeft: '10px'
    },
    small: {
        background: '#dc3545',
        borderRadius: '15px',
        color: 'white'
    }
}));

export default function LockedSources(props) {
    const classes = useStyles();
    const [sourcesLocked, setSourcesLocked] = useState(null)

    const [sourcesLockedHidden, setSourcesLockedHidden] = useState(true)

    const refreshSourcesLock = (source) => {
        setSourcesLocked(sourcesLocked.filter(function (element) { return element.identifier !== source }))
    }

    const loadSourceslock = async () => {
        await Services.getSourcesLocked(props.server._id).then((response) => {
            if (!response.data.errors) {
                setSourcesLocked(response.data)
            }
        }).catch((error) => {
            return
        })
    }

    useEffect(() => {
        async function getSourcesLocked() {
            await Services.getSourcesLocked(props.server._id).then((response) => {
                console.log(response.data)
                if (!response.data.errors) {
                    setSourcesLocked(response.data)
                }
            }).catch((error) => {
                return
            })
        }
        getSourcesLocked();
    }, [props.refreshSourcesLock, props.server._id]);

    return (
        <React.Fragment>
            {
                sourcesLocked !== null && sourcesLocked.length > 0 && props.server !== {} ?
                    <Grid item xs={12} sm={12} >
                        <Paper className={classes.paper}>
                            <Box display="flex" alignItems="center" flexGrow={1}>
                                <Box flexGrow={1}>
                                    <Typography color="textPrimary" variant="h6">
                                        Bloqueados
                                    </Typography>
                                </Box>
                                <Box>
                                    <Typography >
                                        <Badge badgeContent={sourcesLocked.length} color="primary" />
                                    </Typography>
                                </Box>
                            </Box>

                            <div style={{ width: '100%' }}>
                                {
                                    sourcesLocked.length > 0 &&
                                    sourcesLocked.slice(0, 3).map((sourcelock, index) => {
                                        return <LockedSource refreshSourcesLock={refreshSourcesLock} loadSourceslock={loadSourceslock} showMessage={props.showMessage} key={'server' + index} sourcelock={sourcelock} server={props.server} />
                                    })
                                }
                            </div>
                            {
                                sourcesLocked.length > 3 &&
                                <React.Fragment>
                                    <div style={{ width: '100%', display: sourcesLockedHidden ? 'none' : 'inherit' }}>
                                        {
                                            sourcesLocked.slice(3, sourcesLocked.length).map((sourcelock, index) => {
                                                return <LockedSource refreshSourcesLock={refreshSourcesLock} loadSourceslock={loadSourceslock} showMessage={props.showMessage} key={'server' + index} sourcelock={sourcelock} server={props.server} />
                                            })
                                        }
                                    </div>
                                    <Box display="flex" alignItems="center" justifyContent="center">
                                        <Button onClick={() => { setSourcesLockedHidden(!sourcesLockedHidden) }}>{sourcesLockedHidden ? 'Ver todos' : 'Volver a ocultar'}</Button>
                                    </Box>
                                </React.Fragment>
                            }


                        </Paper>
                    </Grid>
                    : ''
            }
        </React.Fragment>
    );
}
