import React, { useState, useEffect } from "react";
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import ControlPointIcon from '@material-ui/icons/ControlPoint';
import ThumbUpAltIcon from '@material-ui/icons/ThumbUpAlt';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import TextField from '@material-ui/core/TextField';
import Grid from '@material-ui/core/Grid';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Alert from '@material-ui/lab/Alert';
import CircularProgress from '@material-ui/core/CircularProgress';
import { green } from '@material-ui/core/colors';
import * as Services from "../../services/services";
import TelegramChatItem from "./telegramchatitem";

var validator = require('validator');

const useStyles = makeStyles((theme) => ({
    paper: {
        padding: theme.spacing(2),
        color: theme.palette.text.secondary,
        borderRadius: '1rem'
    },
    button: {
        margin: '0',
        minWidth: '0'
    },
    typography: {
        marginBottom: '0',
        marginLeft: '10px'
    },
    wrapper: {
        margin: theme.spacing(1),
        position: 'relative',
    },
    buttonProgress: {
        color: green[500],
        position: 'absolute',
        top: '50%',
        left: '50%',
        marginTop: -12,
        marginLeft: -12,
    }
}));

export default function TelegramChats(props) {
    const classes = useStyles();
    const [chats, setChats] = useState(null)

    const [loading, setLoading] = useState(false);

    const [newtelegramchat, setNewTelegramchat] = useState({
        name: '',
        id: ''
    })

    const [error, setError] = useState({
        show: false,
        type: 'error',
        msg: ''
    });

    const [open, setOpen] = useState(false);

    const [teleChatsHidden, setTeleChatsHidden] = useState(true)

    const handleClose = () => {
        setOpen(false);

        setNewTelegramchat({
            name: '',
            id: ''
        })

        setError({
            show: false,
            type: 'error',
            msg: ''
        })
    };

    const handleChange = (e) => {
        e.preventDefault()
        setNewTelegramchat(newtelegramchat => ({ ...newtelegramchat, [e.target.name]: e.target.value }));
    }

    const handleSubmit = async () => {
        setError({
            show: false,
            type: 'error',
            msg: ''
        })
        if (validator.isEmpty(newtelegramchat.name, { ignore_whitespace: true })) {
            setError(error => ({ ...error, show: true, msg: 'El nombre no es vlido' }))
        } else
            if (validator.isEmpty(newtelegramchat.id, { ignore_whitespace: true })) {
                setError(error => ({ ...error, show: true, msg: 'El ID no es vlido' }))
            } else {
                if (!loading) {
                    setLoading(true);
                }
                await Services.registerChat(newtelegramchat.name, newtelegramchat.id)
                    .then((response) => {
                        setLoading(false);
                        if (response.data.errors)
                            setError(error => ({ ...error, show: true, msg: response.data.errors[0].msg }))
                        else {
                            props.showMessage('Usuario autorizado correctamente!', 'success')
                            handleClose()
                            loadChats()
                        }
                    }).catch((error) => {
                        setLoading(false);
                        setError(error => ({ ...error, show: true, msg: 'Lo sentimos ha ocurrido un error inesperado.' }))
                    });
            }
    }

    const loadChats = async () => {
        await Services.getBot().then((response) => {
            if (response.data !== null && response.data.chats !== undefined) {
                setChats(response.data.chats)
            } else setChats(null)
        }).catch((error) => {
            return
        })
    }

    useEffect(() => {
        loadChats()
    }, [props.refreshChats]);

    return (
        <React.Fragment>
            {
                chats !== null ?
                    <Grid item xs={12} sm={12} >
                        <Paper className={classes.paper}>
                            <Box display="flex" alignItems="center" flexGrow={1}>
                                <Box flexGrow={1}>
                                    <Typography color="textPrimary" variant="h6">Autorizados al bot</Typography>
                                </Box>
                                <IconButton onClick={(e) => { e.preventDefault(); setOpen(true) }} edge="end" aria-label="add">
                                    <ControlPointIcon />
                                </IconButton>
                            </Box>
                            <div style={{ width: '100%' }}>
                                {
                                    chats.length > 0 &&
                                    chats.slice(0, 3).map((chat, index) => {
                                        return <TelegramChatItem loadChats={loadChats} showMessage={props.showMessage} key={'botchat' + index} chat={chat} />
                                    })
                                }
                            </div>

                            {
                                chats.length > 3 &&
                                <React.Fragment>
                                    <div style={{ width: '100%', display: teleChatsHidden ? 'none' : 'inherit' }}>
                                        {
                                            chats.slice(3, chats.length).map((chat, index) => {
                                                return <TelegramChatItem loadChats={loadChats} showMessage={props.showMessage} key={'botchat' + index} chat={chat} />
                                            })
                                        }
                                    </div>
                                    <Box display="flex" alignItems="center" justifyContent="center">
                                        <Button onClick={() => { setTeleChatsHidden(!teleChatsHidden) }}>{teleChatsHidden ? 'Ver todos' : 'Volver a ocultar'}</Button>
                                    </Box>
                                </React.Fragment>
                            }
                        </Paper>
                        <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
                            <form onSubmit={handleSubmit}>
                                <DialogTitle id="form-dialog-title">Autorizar usuario al bot</DialogTitle>
                                <DialogContent>

                                    {
                                        error.show && <Alert variant="outlined" severity={error.type} style={{ marginBottom: '1rem' }}>{error.msg}</Alert>
                                    }

                                    <Box style={{ width: '100%', marginTop: '1rem' }}>
                                        <TextField style={{ width: '100%' }}
                                            onChange={handleChange}
                                            id="telegramchat-dnbase"
                                            name="name"
                                            label="Nombre"
                                            variant="outlined"
                                            size="small"
                                            value={newtelegramchat.name}
                                        />
                                    </Box>
                                    <Box style={{ width: '100%', marginTop: '1rem' }}>
                                        <TextField style={{ width: '100%' }}
                                            onChange={handleChange}
                                            id="telegramchat-dnuser"
                                            name="id"
                                            label="ID"
                                            variant="outlined"
                                            size="small"
                                            value={newtelegramchat.id}
                                        />
                                    </Box>

                                </DialogContent>
                                <DialogActions style={{ justifyContent: 'center', marginBottom: '0.5rem' }}>
                                    <div className={classes.wrapper}>
                                        <Button
                                            onClick={handleSubmit}
                                            variant="contained"
                                            color="secondary"
                                            size="small"
                                            className={classes.button}
                                            startIcon={<ThumbUpAltIcon />}
                                            disabled={loading}
                                        >
                                            Aceptar
                                        </Button>
                                        {loading && <CircularProgress size={24} className={classes.buttonProgress} />}
                                    </div>
                                    <Button
                                        onClick={handleClose}
                                        variant="contained"
                                        color="default"
                                        size="small"
                                        className={classes.button}
                                        startIcon={<CloseIcon />}
                                    >
                                        Cancelar
                                    </Button>
                                </DialogActions>
                            </form>
                        </Dialog>
                    </Grid>
                    : ''
            }
        </React.Fragment>
    );
}
