import React, { useState, useEffect, useRef } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Avatar from '@material-ui/core/Avatar';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import logo from '../logo/favicon.png';
import * as Services from '../../services/services'
import Button from '@material-ui/core/Button';
import ThumbUpAltIcon from '@material-ui/icons/ThumbUpAlt';
import Alert from '@material-ui/lab/Alert';
import './login.css'

const validator = require('validator')

const useStyles = makeStyles({
    root: {
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'center',
        height: '100%'
    },
    card: {
        width: '100%',
        maxWidth: '50rem',
        alignItems: 'center',
    },
    cardcontent: {
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        margin: '1rem'
    },
    bullet: {
        display: 'inline-block',
        margin: '0 2px',
        transform: 'scale(0.8)',
    },
    title: {
        fontSize: 14,
    },
    pos: {
        marginBottom: 12,
    }
});

export default function Login(props) {
    const classes = useStyles();
    let formLogin = useRef(null);

    const [username, setUserName] = useState();
    const [password, setPassword] = useState();

    const [error, setError] = useState({
        show: false,
        type: 'error',
        msg: ''
    });

    const handleSubmit = async (e) => {
        e.preventDefault()
        setError({
            show: false,
            type: 'error',
            msg: ''
        })

        if (validator.isEmpty(username, { ignore_whitespace: true })) {
            setError(true)
        } else if (validator.isEmpty(password, { ignore_whitespace: true })) {
            setError(true)
        } else {
            await Services.login({
                username: username,
                password: password
            }).then((response) => {
                if (response.data.errors) {
                    setError(error => ({ ...error, show: true, msg: response.data.errors[0].msg }))
                } else {
                    props.setToken(response.data)
                }
            }).catch((err) => {
                setError(error => ({ ...error, show: true, msg: "Lo sentimos ha ocurrido un error inesperado." }))
            })
        }
    }

    useEffect(() => {

    }, []);

    return (
        <div className={classes.root}>
            <Avatar style={{ width: '70px', height: '70px', marginBottom: '2rem' }} alt="logo" src={logo} />

            <form ref={formLogin} style={{ width: '19rem' }} onSubmit={handleSubmit}>

                <Card className={classes.card}>
                    <CardContent className={classes.cardcontent}>

                        <Typography
                            style={{ marginTop: '1rem' }}
                        >
                            Autenticacin requerida
                        </Typography>

                        {
                            error.show && <Alert variant="outlined" severity={error.type} style={{ marginTop: '1rem', marginBottom: '1rem', width: '100%' }}>{ error.msg }</Alert>
                        }

                        <TextField style={{ width: '100%', marginBottom: '15px' }}
                            required
                            label="Usuario"
                            onChange={(e) => { setUserName(e.target.value) }}
                        />

                        <TextField style={{ width: '100%', marginBottom: '15px' }}
                            required
                            type="password"
                            label="Contrasea"
                            onChange={(e) => { setPassword(e.target.value) }}
                        />

                        <Button style={{ marginTop: '1.5rem' }}
                            type='submit'
                            color="secondary"
                            startIcon={<ThumbUpAltIcon />}
                            variant="contained">
                            Entrar
                        </Button>

                    </CardContent>
                </Card>
            </form>
        </div>

    );
}