import React from 'react';
import Button from '@material-ui/core/Button';
import Avatar from '@material-ui/core/Avatar';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import Dialog from '@material-ui/core/Dialog';
import PersonIcon from '@material-ui/icons/Person';
import Typography from '@material-ui/core/Typography';
import ThumbUpAltIcon from '@material-ui/icons/ThumbUpAlt';
import DesktopWindowsIcon from '@material-ui/icons/DesktopWindows';
import Box from '@material-ui/core/Box';
import Chip from '@material-ui/core/Chip';
import * as Services from "../../services/services";

var validator = require('validator');

export default function LdapDialog(props) {

  return (
    <Dialog aria-labelledby="simple-dialog-title" open={props.ldapdialog.open}>
      <DialogContent>
        {
          props.ldapdialog.ldapinfo !== null ?
            <Box m={1}>
              <Box display='flex' flexDirection='column' alignItems='center'>
                <Avatar style={{ width: '4rem', height: '4rem' }}>
                  {
                    validator.isIP(Services.parseSource(props.ldapdialog.groupData.value)) ?
                      <DesktopWindowsIcon /> :
                      <PersonIcon style={{ fontSize: 60 }} />
                  }
                </Avatar>
                <Typography variant="h6"><b>{props.ldapdialog.ldapinfo.cn}</b></Typography>
                <Typography>{Services.parseSource(props.ldapdialog.groupData.value)}</Typography>
                {
                  props.ldapdialog.ldapinfo.memberOf !== undefined ?
                    <Box display='flex' alignItems='center' flexWrap='wrap' justifyContent='center'>
                      {
                        props.ldapdialog.ldapinfo.memberOf.map((group, index) => {
                          let splitgroup = (group.split(','))[0].split('=')[1] 
                          return <Chip label={splitgroup} style={{ margin: '1px' }} />
                        })
                      }
                    </Box>
                    : ''
                }
              </Box>
            </Box>
            : ''
        }
      </DialogContent>
      <DialogActions style={{ justifyContent: 'center', marginBottom: '1rem'}}>
        <Button onClick={() => { props.handleClose() }} variant="contained" color="secondary" size="small" >
          <ThumbUpAltIcon />
          <Typography>Aceptar</Typography>
        </Button>
      </DialogActions>
    </Dialog>
  );
}
